export default class LeafletOption {
    /**
     *
     * @param latlngs
     * @param iconList
     * @param color
     * @param fillColor
     * @param fillOpacity
     * @param opacity
     */
    constructor(data) {

        if (data == null) return;

        if (typeof data === "object" && !!data.length) {
            this.latlngs = data;
            this.iconList = []; //Список иконок для отображения WIP
            this.color = null;
            this.fillColor = null;
            this.fillOpacity = null;
            this.opacity = null;
        } else {
            this.latlngs = data.latlngs;
            this.iconList = data.iconList; //Список иконок для отображения WIP
            this.color = data.color;
            this.fillColor = data.fillColor;
            this.fillOpacity = data.fillOpacity;
            this.opacity = data.opacity;
        }

    }
}