<template xmlns:v-slot="http://www.w3.org/1999/XSL/Transform">
    <span flat>
        <v-overlay :value="loading" v-if="loading">
            <v-progress-circular indeterminate size="64"></v-progress-circular>
        </v-overlay>
        <v-dialog v-else
                  v-model="dialog"
                  filled
                  width="30%"
                  style="z-index:9999;"
        >
            <template v-slot:activator="{ on, attrs }">
                <v-btn dark fab class="mr-3"
                       color="green"
                       v-on="on"
                       small>
                    <v-icon>fa-save</v-icon>
                </v-btn>
            </template>

            <v-card>
                <v-card-title class="grey lighten-2">
                    Save map info
                </v-card-title>

                <v-card-text>
                    <v-row justify="center">
                        <v-text-field
                                class="mt-2"
                                label="Name"
                                v-model="saveName"
                                outlined
                                hide-details
                                clearable
                        ></v-text-field>

                        <v-btn :disabled="copyEnabled" class="success mt-2" width="100%" @click="saveDataOnServer">
                            Save
                        </v-btn>

                        <template v-if="copyEnabled && viewHash">
                            <v-text-field
                                    outlined
                                    class="mt-4"
                                    id="forView"
                                    hide-details
                                    :value="getViewUrl()"
                            ></v-text-field>
                            <v-btn color="yellow" class="mt-2" width="100%" @click="clipboardSourceCode('forView')">
                                COPY
                            </v-btn>
                        </template>
                    </v-row>
                </v-card-text>

                <v-divider></v-divider>

                <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn
                            color="error"
                            text
                            @click="markDownPathDialog = false"
                    >
                        Close
                    </v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>
    </span>
</template>

<script>
    import {mapActions, mapGetters, mapMutations} from "vuex";

    export default {
        name: "SaveUrlMenu",
        data: () => ({
            saveName: 'save ' + new Date().toISOString(),
            loading: true,
            markDownPathDialog: false,
            viewHash: null,
            copyEnabled: false,
        }),

        mounted() {
            //
            // let view = this.$route.query.view_hash;
            //
            // if (view) {
            //     this.loading = true;
            //     this.requestGetData(view).then(
            //         (response) => {
            //             this.loadStore(response.data.content);
            //             this.saveName = response.data.name;
            //             this.loading = false;
            //         },
            //         (error) => {
            //             this.loading = false;
            //         });
            //
            // }

            this.loadFormUrl();

        },

        watch: {
            markDownPathDialog: (newValue, oldValue) => {
                // if (newValue) {
                //     this.copyEnabled = false;
                // }
            },

            '$route.query.view_hash': (newValue, oldValue) => {
                this.loadFormUrl();
            }


        },
        methods: {
            ...mapActions({
                requestSaveData: 'REQUEST_SAVE_DATA_ON_SERVER',
                requestGetData: 'REQUEST_DATA_FROM_SERVER',
            }),

            ...mapMutations({
                loadStore: 'LOAD_STORE',
            }),

            loadFormUrl() {
                let view = this.$route.query.view_hash;

                if (view) {
                    this.loading = true;
                    this.requestGetData(view).then(
                        (response) => {
                            this.loadStore(response.data.content);
                            this.saveName = response.data.name;
                            this.loading = false;
                        },
                        (error) => {
                            this.loading = false;
                        });

                }else{
                    this.loading = false;
                }
            },

            saveDataOnServer() {
                this.loading = true;
                let toSave = {
                    name: this.saveName,
                    content: this.getStore,
                };

                this.requestSaveData(toSave).then((response) => {
                    this.copyEnabled = true;

                    if(response.data) {
                        this.viewHash = response.data.view_hash;
                        this.saveName = response.data.name;
                    }

                    this.loading = false;
                }, (error) => {
                    this.loading = false;
                });

            },

            getViewUrl() {
                return window.location.origin + "/#/?map=" + this.currentMap + '&view_hash=' + this.viewHash;
            },
            clipboardSourceCode(code) {
                // let exportCode = this.$refs.exportCode.$el.querySelector('input')
                // exportCode.select()
                // document.execCommand("copy");

                if (code === 'forView') {
                    this.copyEnabled = false;
                }
                let input = document.getElementById(code);
                input.select();
                document.execCommand("copy");
            },
        },
        computed: {
            ...mapGetters({
                currentMap: 'GET_CURRENT_MAP',
                getStore: 'GET_STORE',
            })
        }
    }
</script>

