import LeafletOption from "./LeafletOption";
import GameInfo from "./GameInfo";

export default class GameLocation {
    constructor(leaflet, game) {
        this.leaflet = leaflet || new LeafletOption();
        this.game = game || new GameInfo();

        if(!game || !leaflet)return;


        let gameType = game.type;
        let gameFraction = game.fraction;

        leaflet.fillOpacity = 0.45;
        leaflet.opacity = 1;

        if (game && gameType === 'BLACK') {
            leaflet.color = '#000000';
            leaflet.fillColor = '#6b6d6d';
        }else if (game && gameType === 'RED') {
            leaflet.color = '#ff001d';
            leaflet.fillColor = '#ffbcbf';
        }else if (game && gameType === 'ORANGE') {
            leaflet.color = '#ff9e00';
            leaflet.fillColor = '#ffd5ba';
        }

        if (gameFraction !== GameInfo.staticFraction.EMPTY.code) {
            leaflet.color = GameInfo.staticFraction[gameFraction].fillColor;
            leaflet.fillColor = GameInfo.staticFraction[gameFraction].color;
        }
    }
}