<template xmlns:v-slot="http://www.w3.org/1999/XSL/Transform">
    <div>
        <v-overlay
                absolute
                :value="roaDataLoading"
        >
            <v-progress-circular indeterminate size="64"></v-progress-circular>
        </v-overlay>

        <v-card flat>

            <v-card-title
                    class="grey lighten-2"
                    primary-title
                    v-if="$route.name === 'road-tunnel'"
            >
                Resource "Roads of Avalon"

                <v-btn
                        fab
                        target="_blank"
                        href="https://docs.google.com/document/d/1wglDCiQp6O0sdOyYAajGVZ56qADRqUWUoDXFxufVPBk/edit"
                        x-small
                        class="ma-1 pa-1 yellow"
                        title="Roads of Avalon Data Exploration"
                >
                    <v-icon>
                        fa-file-alt
                    </v-icon>
                </v-btn>

                <v-spacer></v-spacer>
                <!--<v-btn icon @click="markDownPathDialog = false">-->
                <!--<v-icon>mdi-close</v-icon>-->
                <!--</v-btn>-->
            </v-card-title>
            <template v-if="$route.name === 'road-data'">

                <v-switch
                        class="mr-3"
                        v-model="isNotReversePath"
                        style="display: inline-block;"
                        label="Path direction"
                ></v-switch>

                <v-switch v-if="this.additionalRoaData !== null"
                          v-model="additionalRoaData"
                          style="display: inline-block;"
                          label="Resource"
                ></v-switch>

                <v-btn class="error ml-3" @click="resetTunnelArray()">CACHE RESET</v-btn>

                <v-dialog v-model="markDownPathDialog" width="70%">
                    <template v-slot:activator="{ on, attrs }">
                        <v-btn
                                class="ml-1"
                                color="primary"
                                dark
                                v-bind="attrs"
                                v-on="on"
                        >
                            Markdown Path Info
                        </v-btn>
                    </template>
                    <v-card>
                        <v-card-title class="title">Path info</v-card-title>
                        <v-card-text>
                            <div v-for="info in getMarkdownPortalPathList()">
                                {{info.name}}
                                <span v-if="info.maxPortal">({{info.maxPortal}})</span>
                                <span v-if="info.time && info.time.value > 0"> `{{info.time.hours}}ч {{info.time.minutes}}м`</span>
                            </div>
                        </v-card-text>
                        <v-card-actions>
                            <v-spacer></v-spacer>
                            <v-btn color="green darken-1" text @click="markDownPathDialog = false">Close</v-btn>
                        </v-card-actions>
                    </v-card>
                </v-dialog>

            </template>
            <template v-if="$route.name === 'road-tunnel'">
                <v-autocomplete
                        dense
                        class="ma-1"
                        v-model="locationFilter"
                        :items="tunnelArray"
                        :item-text="(item) =>{ return item['name'] }"
                        multiple
                        outlined
                        label="location"
                        style="width:300px; display:inline-block;"
                        clearable
                ></v-autocomplete>
                <v-autocomplete
                        dense
                        class="ma-1"
                        v-model="clusterTypeFilter"
                        :items="clusterTypeList"
                        multiple
                        outlined
                        label="location type"
                        style="width:250px; display:inline-block;"
                        clearable
                ></v-autocomplete>
                <v-autocomplete
                        dense
                        class="ma-1"
                        v-model="markerFilter"
                        :items="markerType"
                        multiple
                        outlined
                        label="location marker"
                        style="width:300px; display:inline-block;"
                        clearable
                ></v-autocomplete>

                <v-select
                        dense
                        class="ma-1"
                        v-model="resourceFilter"
                        :items="Object.keys(resourceIcon)"
                        multiple
                        outlined
                        label="resource type"
                        style="width:200px; display:inline-block;"
                        clearable

                ></v-select>
                <v-select
                        dense
                        class="ma-1"
                        v-model="tierFilter"
                        :items="[1,2,3,4,5,6,7,8]"
                        multiple
                        outlined
                        label="resource tier"
                        style="width:200px; display:inline-block;"
                        clearable
                ></v-select>

                <v-text-field
                        dense
                        v-model="quantityFilter"
                        class="ma-1"
                        prefix=">="
                        label="spots quantity"
                        style="width:200px; display:inline-block;"
                        outlined
                        clearable
                >
                </v-text-field>

                <v-btn class="success ma-1" @click="doFilteredTunnelArray()" :loading="resourceFilterLoading"> FILTER
                </v-btn>
                <v-btn class="error ma-1" @click="filteredTunnelArray = []" :loading="resourceFilterLoading"
                       :disabled="!filteredTunnelArray.length > 0"> CLEAR
                </v-btn>

                <span>

                <v-checkbox style="display: inline-block" v-model="isResource" class="mx-2"
                            label="resource"></v-checkbox>
                <v-checkbox style="display: inline-block" v-model="isMarker" class="mx-2" label="marker"></v-checkbox>
            </span>
            </template>
            <v-card-text>
                <v-row v-if="roaDataSorted && $route.name === 'road-data'">
                    <v-col cols="12">
                        <v-timeline>
                            <v-timeline-item
                                    v-for="(timeCluster, timeIndex) in roaDataSorted"
                                    :key="timeIndex"
                                    :color="getEnterClusterInfo(timeCluster)[(isNotReversePath)?'reverse':'normal'].color"
                                    dense
                                    right
                                    dark

                            >
                                <template v-slot:opposite>
                                    <template
                                            v-if="timeCluster.enterFromCluster != null && getEnterClusterInfo(timeCluster)">
                                        <span class="subtitle-1"
                                              :style="{opacity: (getRemainingTime(getEnterClusterInfo(timeCluster))) > 0 ? 1 : 0.3}"><b>
                                            <template v-if="isNotReversePath">
                                                {{getEnterClusterInfo(timeCluster).reverse.name}}
                                            </template>
                                            <template v-else>
                                                {{getEnterClusterInfo(timeCluster).normal.name}}
                                            </template>
                                        </b></span><br>
                                        <span class="title"
                                              v-if="getEnterClusterInfo(timeCluster).normal.timeLeft || getEnterClusterInfo(timeCluster).reverse.timeLeft"
                                              :style="{opacity: (getRemainingTime(getEnterClusterInfo(timeCluster))) > 0 ? 1 : 0.25}">
                                                <!--{{getEnterClusterInfo(timeCluster).normal.timeLeft || getEnterClusterInfo(timeCluster).reverse.timeLeft}}-->
                                            <countdown :time="getRemainingTime(getEnterClusterInfo(timeCluster))">
                                                <template slot-scope="props"><span
                                                        v-if="props.days">{{ props.days }} д</span> <span
                                                        v-if="props.hours">{{ props.hours }} ч</span> {{ props.minutes }} м {{props.seconds}} с</template>
                                            </countdown>
                                        </span>

                                    </template>
                                    <template v-else-if="timeCluster.blank">
                                        <template v-if="timeIndex == 0">
                                            {{isNotReversePath ? 'END' : 'START'}}
                                        </template>
                                        <template v-else>
                                            {{!isNotReversePath ? 'END' : 'START'}}
                                        </template>

                                    </template>
                                    <template v-else-if="timeCluster.code.indexOf('@HIDEOUT@') != -1">
                                        Hideout
                                    </template>
                                    <template v-else>
                                        ?
                                    </template>

                                </template>
                                <v-card dense outlined v-if="!timeCluster.blank && additionalRoaData">
                                    <v-card-title class="title">{{timeCluster.name}}</v-card-title>
                                    <v-card-text>
                                        <template v-for="tunnel in timeCluster.portalList">
                                            <template v-for="item in tunnelArray">
                                                <div v-if="item.name == tunnel.name">
                                                    <v-card outlined class="ma-1 pa-1">
                                                        <v-card-text>
                                                <span class="title">{{item["name"]}} {{to_roman(item["tier"])}}
                                                    <v-card style="display:inline-block" flat outlined
                                                            class="pa-1 ma-1">
                                                        <!-- TYPE -->
                                                        <v-chip
                                                                class="ma-1 blue-grey darken-1"
                                                                text-color="white"
                                                        >
                                                            {{item["type"]}}
                                                        </v-chip>
                                                    </v-card>

                                                     <span
                                                             v-if="getRemainingTimeByTunnel(tunnel) != null">
                                                        <span :style="{color:getPortalColorByEnterNumber(tunnel.maxEnterCount)}">⬤ </span>
                                                         <!-- Счётчик для расширенной инфы по локациям -->
                                                         <countdown :time="getRemainingTimeByTunnel(tunnel)">
                                                             <template slot-scope="props">{{ props.days }} д {{ props.hours }} ч {{ props.minutes }} м {{props.seconds}}</template>
                                                        </countdown>
                                                    </span>
                                                    <span v-else> ? </span>

                                                    <template v-if="isMarker">
                                                    </br>MapMarker
                                                    <v-card style="display:inline-block" flat outlined
                                                            class="pa-1 ma-1 mb-5" v-if="item.markerInfo">
                                                        <template v-for="(markerValue, markerKey) in item.markerInfo">
                                                            <v-chip
                                                                    class="ma-1 blue-grey darken-1"
                                                                    text-color="white"
                                                                    v-if="markerValue > 0"

                                                            >
                                                                <v-avatar
                                                                        left
                                                                        class="blue-grey darken-4"
                                                                >
                                                                    {{markerValue}}
                                                                </v-avatar>
                                                                {{markerKey}}
                                                            </v-chip>
                                                        </template>
                                                    </v-card>
                                                    </template>
                                                </span>
                                                            <v-row v-if="item.resourceList && isResource">
                                                                <template
                                                                        v-for="(resource, rindex) in item.resourceList">
                                                    <span v-if="resource && resource['tier']  && resource['name'] && resourceIcon[resource['name']][resource['tier']]">
                                                    <v-badge
                                                            color="green"
                                                            :content="resource['quantity']"
                                                            offset-x="25"
                                                    >
                                                        <v-btn width="50" height="50" class="ma-1 pa-0" readonly>
                                                            <!--<v-img :src="resourceIcon[resource['@name']][resource['@tier']]"></v-img>-->
                                                            <v-img :src="resourceIcon[resource['name']][resource['tier']]"></v-img>
                                                        </v-btn>
                                                    </v-badge>
                                                </span>
                                                                </template>
                                                                <!--<template v-if="item.resourceList.length === 0">-->
                                                                <!--<p>Location without resource!</p>-->
                                                                <!--</template>-->
                                                            </v-row>

                                                        </v-card-text>
                                                    </v-card>

                                                </div>
                                            </template>
                                        </template>
                                    </v-card-text>
                                </v-card>
                                <template v-else-if="!timeCluster.blank">
                                    <div class="mt-2">
                                        {{timeCluster.name}}
                                        <span v-if="getEnterClusterInfo(timeCluster).isTunnel()"> ({{ getEnterClusterInfo(timeCluster).getAnyMaxEnterCount() }}) </span>
                                    </div>
                                </template>

                            </v-timeline-item>
                        </v-timeline>
                    </v-col>
                </v-row>
                <!--<v-card class="pa-2" outlined v-if="roaData">-->

                <!--&lt;!&ndash; GENERAL_OBJECT &ndash;&gt;-->
                <!--<template v-for="(clusterObject,code) in roaData">-->
                <!--<p class="title">Location {{clusterObject['name']}}</p>-->

                <!--&lt;!&ndash; PORTAL_LIST &ndash;&gt;-->
                <!--<template v-for="(portalObject, portalKey) in clusterObject.portalList">-->
                <!--<div> {{portalObject.name}}-->
                <!--<v-icon :color="(getPortalColorByEnterNumber(portalObject.maxEnterCount))">fa-circle-->
                <!--</v-icon>-->
                <!--{{portalObject.timeLeft}}-->
                <!--</div>-->
                <!--</template>-->

                <!--</template>-->
                <!--</v-card>-->
                <!-- :style="{'min-height': item.resourceList}" -->

                <template v-for="(item, tindex) in filteredTunnelArray">
                    <!--<template v-for="(item, tindex) in tunnelArray">-->
                    <v-card flat :style="{'max-height':200*filteredTunnelArray.length}">
                    <v-lazy
                            value="true"
                            :options="{
                                              threshold: .5
                                            }"
                            min-height="50"
                            transition="fade-transition"
                    >
                    <v-sheet min-height="50" class="fill-height" color="transparent" v-if="item">
                        <v-card outlined class="ma-1 pa-1">
                            <v-card-text>

                                <p class="title">{{item["name"]}}
                                    <v-card style="display:inline-block" flat outlined class="pa-1 ma-1">
                                        <!-- TIER -->
                                        <v-chip
                                                class="ma-1 blue-grey darken-1"
                                                text-color="white"
                                        >
                                            <v-avatar
                                                    class="blue-grey darken-4"
                                            >
                                                {{to_roman(item["tier"])}}
                                            </v-avatar>
                                        </v-chip>

                                        <!-- TYPE -->
                                        <v-chip
                                                class="ma-1 blue-grey darken-1"
                                                text-color="white"
                                        >
                                            {{item["type"]}}
                                        </v-chip>
                                    </v-card>

                                    <template v-if="isMarker">
                                        MapMarker
                                        <v-card style="display:inline-block" flat outlined class="pa-1 ma-1"
                                                v-if="item">
                                            <template v-for="(markerValue, markerKey) in item.markerInfo">
                                                <v-chip
                                                        class="ma-1 blue-grey darken-1"
                                                        text-color="white"
                                                        v-if="markerValue > 0"

                                                >
                                                    <v-avatar
                                                            left
                                                            class="blue-grey darken-4"
                                                    >
                                                        {{markerValue}}
                                                    </v-avatar>
                                                    {{markerKey}}
                                                </v-chip>
                                            </template>
                                        </v-card>
                                    </template>
                                </p>

                                <v-row v-if="item.resourceList && isResource" >
                                    <template v-for="(resource, rindex) in item.resourceList" v-if="resource">
                                        <span v-if="resource['name'] && resource['tier'] && resourceIcon[resource['name']][resource['tier']]">
                                        <v-badge
                                                color="green"
                                                :content="resource['quantity']"
                                                offset-x="25"
                                        >
                                            <v-btn width="50" height="50" class="ma-1 pa-0" readonly
                                                   v-if="resource['name']">
                                                <!--<v-img :src="resourceIcon[resource['@name']][resource['@tier']]"></v-img>-->
                                                <v-img :src="resourceIcon[resource['name']][resource['tier']]"></v-img>
                                            </v-btn>
                                        </v-badge>
                                    </span>
                                    </template>
                                    <!--</v-lazy>-->
                                    <!--<template v-if="item.resourceList.length === 0">-->
                                        <!--<p>Location without resource!</p>-->
                                    <!--</template>-->
                                </v-row>

                            </v-card-text>
                        </v-card>
                    </v-sheet>
                    </v-lazy>
                    </v-card>
                </template>
            </v-card-text>

        </v-card>
    </div>
</template>

<script>
    /*
"@id": "TNL-202",
"@file": "TNL-002_RDS_FR_RED_T4_AVA_AVA.cluster.xml",
"@displayname": "Curites-Exakrom",
"@enabled": "true",
"@type": "TUNNEL_ROYAL",
"@timeregion": "BR_00_00",
"@rareresourcedistribution": "DEFAULT",
"@speciallocation": "",
"@origin": "-925 -775",
"@size": "1580 1420",
"@categoryname": "",
"@editoroffset": "7100 -240",
"@minimapBoundsMin": "-840 -700",
"@minimapBoundsMax": "460 600",
"@minimapHeightRange": "0 12",
"@minimapBaseLevel": "6",
"exits": null,
"minimapmarkers": {
"marker": [
* */
    //import WorldJson from '../assets/json/world';
    import Location from '../assets/json/location';
    import Cluster from "../model/Cluster";
    import {mapActions} from "vuex";

    export default {
        name: "Road",
        data: () => ({
            markDownPathDialog: false,
            updateTime: null,
            lazyMode: true,
            resourceFilterLoading: false,
            roaDataLoading: true,
            isNotReversePath: true,
            additionalRoaData: null,

            intervalRoaPolling: null,
            roaData: null,
            uid: null,
            roaDataSorted: [],

            isResource: true,
            isMarker: true,

            locationFilter: [],
            resourceFilter: [],
            markerFilter: [],
            clusterTypeFilter: [],
            tierFilter: [],
            quantityFilter: 0,

            font_ar: [1, 4, 5, 9, 10, 40, 50, 90, 100, 400, 500, 900, 1000, 4000, 5000, 9000, 10000],
            font_rom: ["I", "IV", "V", "IX", "X", "XL", "L", "XC", "C", "CD", "D", "CM", "M",
                "M&#8577;", "&#8577;", "&#8577;&#8578;", "&#8578;"],
            resourceIcon: {
                ROCK: {
                    "1": "https://wiki.albiononline.com/images/thumb/3/3f/Rough_Stone.png/65px-Rough_Stone.png",
                    "2": "https://wiki.albiononline.com/images/thumb/4/4e/Limestone.png/65px-Limestone.png",
                    "3": "https://wiki.albiononline.com/images/thumb/d/d6/Sandstone.png/65px-Sandstone.png",
                    "4": "https://wiki.albiononline.com/images/thumb/0/02/Travertine.png/65px-Travertine.png",
                    "5": "https://wiki.albiononline.com/images/thumb/4/4a/Granite.png/65px-Granite.png",
                    "6": "https://wiki.albiononline.com/images/thumb/9/97/Slate.png/65px-Slate.png",
                    "7": "https://wiki.albiononline.com/images/thumb/2/22/Basalt.png/65px-Basalt.png",
                    "8": "https://wiki.albiononline.com/images/thumb/8/82/Marble.png/65px-Marble.png",
                },
                HIDE: {
                    "1": "https://wiki.albiononline.com/images/thumb/a/af/Rabbit_Hide.png/65px-Rabbit_Hide.png",
                    "2": "https://wiki.albiononline.com/images/thumb/c/cb/Fox_Hide.png/65px-Fox_Hide.png",
                    "3": "https://wiki.albiononline.com/images/thumb/c/c2/Wolf_Hide.png/65px-Wolf_Hide.png",
                    "4": "https://wiki.albiononline.com/images/thumb/7/7b/Boar_Hide.png/65px-Boar_Hide.png",
                    "5": "https://wiki.albiononline.com/images/thumb/3/39/Bear_Hide.png/65px-Bear_Hide.png",
                    "6": "https://wiki.albiononline.com/images/thumb/6/68/Direwolf_Hide.png/65px-Direwolf_Hide.png",
                    "7": "https://wiki.albiononline.com/images/thumb/d/d5/Direboar_Hide.png/65px-Direboar_Hide.png",
                    "8": "https://wiki.albiononline.com/images/thumb/d/d7/Direbear_Hide.png/65px-Direbear_Hide.png",
                },
                ORE: {
                    "1": "_",
                    "2": "https://wiki.albiononline.com/images/thumb/7/78/Copper_Ore.png/65px-Copper_Ore.png",
                    "3": "https://wiki.albiononline.com/images/thumb/c/c4/Tin_Ore.png/65px-Tin_Ore.png",
                    "4": "https://wiki.albiononline.com/images/thumb/8/87/Iron_Ore.png/65px-Iron_Ore.png",
                    "5": "https://wiki.albiononline.com/images/thumb/3/34/Titanium_Ore.png/65px-Titanium_Ore.png",
                    "6": "https://wiki.albiononline.com/images/thumb/0/02/Runite_Ore.png/65px-Runite_Ore.png",
                    "7": "https://wiki.albiononline.com/images/thumb/6/6b/Meteorite_Ore.png/65px-Meteorite_Ore.png",
                    "8": "https://wiki.albiononline.com/images/thumb/2/29/Adamantium_Ore.png/65px-Adamantium_Ore.png",
                },
                FIBER: {
                    "1": "_",
                    "2": "https://wiki.albiononline.com/images/thumb/c/c3/Cotton.png/65px-Cotton.png",
                    "3": "https://wiki.albiononline.com/images/thumb/5/50/Flax.png/65px-Flax.png",
                    "4": "https://wiki.albiononline.com/images/thumb/8/8d/Hemp.png/65px-Hemp.png",
                    "5": "https://wiki.albiononline.com/images/thumb/3/37/Skyflower.png/65px-Skyflower.png",
                    "6": "https://wiki.albiononline.com/images/thumb/9/95/Redleaf_Cotton.png/65px-Redleaf_Cotton.png",
                    "7": "https://wiki.albiononline.com/images/thumb/3/3a/Sunflax.png/65px-Sunflax.png",
                    "8": "https://wiki.albiononline.com/images/thumb/f/f6/Ghost_Hemp.png/65px-Ghost_Hemp.png",
                },
                WOOD: {
                    "1": "https://wiki.albiononline.com/images/thumb/c/c7/Rough_Logs.png/65px-Rough_Logs.png",
                    "2": "https://wiki.albiononline.com/images/thumb/6/6a/Birch_Logs.png/65px-Birch_Logs.png",
                    "3": "https://wiki.albiononline.com/images/thumb/6/6a/Chestnut_Logs.png/65px-Chestnut_Logs.png",
                    "4": "https://wiki.albiononline.com/images/thumb/5/57/Pine_Logs.png/65px-Pine_Logs.png",
                    "5": "https://wiki.albiononline.com/images/thumb/6/63/Cedar_Logs.png/65px-Cedar_Logs.png",
                    "6": "https://wiki.albiononline.com/images/thumb/1/11/Bloodoak_Logs.png/65px-Bloodoak_Logs.png",
                    "7": "https://wiki.albiononline.com/images/thumb/5/50/Ashenbark_Logs.png/65px-Ashenbark_Logs.png",
                    "8": "https://wiki.albiononline.com/images/thumb/4/40/Whitewood_Logs.png/65px-Whitewood_Logs.png",
                }
            },
            tunnelArray: [],
            filteredTunnelArray: [],
        }),
        mounted() {

            if (this.$route.name !== 'road-data') this.roaDataLoading = false;
            if (this.$route.query.additional == 'true') this.additionalRoaData = false;
            if (this.$route.query.uid) this.uid = this.$route.query.uid;

            if (this.intervalRoaPolling) {
                clearInterval(this.intervalRoaPolling);
            }

            /** Поиск данных */
            if (this.$route.name == 'road-data' && this.uid) {
                this.intervalRoaPolling = setInterval(() => {
                    this.requestRoaData(this.uid).then((data) => {
                        //console.log('requestRoaData', JSON.parse(JSON.parse(data.data.content)))
                        this.roaData = JSON.parse(JSON.parse(data.data.content));
                        this.updateTime = new Date(data.data.created);
                        let roaDataSorted = [];
                        roaDataSorted[0] = {blank: '1'};

                        Object.keys(this.roaData).forEach((clusterKey) => {
                            let cluster = this.roaData[clusterKey];
                            roaDataSorted[cluster.ordering] = cluster;
                        });
                        roaDataSorted[roaDataSorted.length] = {blank: '2'};
                        this.roaDataSorted = roaDataSorted.reverse();

                        this.getMarkdownPortalPathList();
                        console.log(this.getMarkdownPortalPathList());

                        this.roaDataLoading = false;
                    })
                }, 2000);
            } else {
                this.roaDataLoading = false;
                this.$router.push({'name': 'road-tunnel'});
            }

            //console.log('WorldJson', WorldJson);
            let markerListType = [];

            let tunnelArrayJson = localStorage.getItem('tunnelArray');
            if (tunnelArrayJson && this.$route.name === 'road-data') {
                this.tunnelArray = JSON.parse(tunnelArrayJson);
                return;
            }

            Location.forEach((clusterInfo) => {
                this.tunnelArray.push(new Cluster(clusterInfo,true));
            });

            /** Подготовка tunnelArray, требуется только для первой инициализации и обновления
             * файла location.json (сделано для того, чтобы жрало меньше трафика)

            WorldJson.world.clusters.cluster.forEach((cluster) => {
                let clasterType = cluster["@type"];

                //if (cluster["@id"].indexOf('TNL-') > -1 && cluster["@minimapBaseLevel"]) {
                if (Cluster.clusterTypeList.indexOf(clasterType) != -1) {
                    //if (["GUILDISLAND","PLAYERISLAND"].indexOf(clasterType) == -1) {

                    this.tunnelArray.push(new Cluster(cluster));
                    // if (cluster['minimapmarkers']['marker']) {
                    //     cluster['minimapmarkers']['marker'].forEach((marker) => {
                    //         markerListType.push(marker['@type']);
                    //     })
                    // }
                }

                localStorage.setItem('tunnelArray', JSON.stringify(this.tunnelArray))

            });
            */

            //console.log('tunnelArray', this.tunnelArray[5]);
            let allString = '';
            // this.tunnelArray.forEach((location) =>{
            //     //allString += location.id+':'+location.name+'\n';
            //     allString += 'albionClusterData.Add("'+location.id+'","'+location.name+'");'+'\n';
            // });

            console.log('allString', allString);
        },
        computed: {
            markerType() {
                //return Cluster.markerType;
                let markerTypeList = [];
                Object.keys(Cluster.markerType).forEach((key) => {
                    //markerTypeList.push({text: Cluster.markerType[key], value: key });//Cluster.markerType[key]
                    markerTypeList.push(Cluster.markerType[key]);//Cluster.markerType[key]
                });

                return markerTypeList;
            },

            clusterTypeList(){
                return Cluster.clusterTypeList;
            }
        },
        methods: {
            ...mapActions({
                requestRoaData: 'REQUEST_ROA_DATA_FROM_SERVER'
            }),

            /**
             * преобразование мс в объект с ч,м,с
             * @param ms
             * @returns {*}
             */
            msToHHMMSS(ms) {
                try {
                    // 1- Convert to seconds:
                    var seconds = ms / 1000;
                    // 2- Extract hours:
                    var hours = parseInt(seconds / 3600); // 3,600 seconds in 1 hour
                    seconds = seconds % 3600; // seconds remaining after extracting hours
                    // 3- Extract minutes:
                    var minutes = parseInt(seconds / 60); // 60 seconds in 1 minute
                    // 4- Keep only seconds not extracted to minutes:
                    seconds = seconds % 60;
                    //return ( hours+":"+minutes+":"+seconds);
                    return {
                        value: ms,
                        hours: hours.toFixed(0),
                        minutes: minutes.toFixed(0),
                        seconds: seconds.toFixed(0),
                    }
                } catch (e) {
                    return {};
                }
            },
            getMarkdownPortalPathList() {
                //forEach(timeCluster) (, timeIndex) in roaDataSorted
                let path = [];
                this.roaDataSorted.forEach((timeCluster) => {
                    if (timeCluster.blank) {
                        path.push({
                            name: timeCluster.name
                        });
                    }
                    ;
                    //<template v-if="timeCluster.enterFromCluster != null && getEnterClusterInfo(timeCluster)">
                    let fromEnterCluster = null;
                    let timeLeftObj = null;
                    let maxCountPortal = null;

                    if (timeCluster.enterFromCluster != null) {
                        fromEnterCluster = this.getEnterClusterInfo(timeCluster);
                        maxCountPortal = fromEnterCluster.getAnyMaxEnterCount();
                    }

                    timeLeftObj = this.msToHHMMSS(this.getRemainingTime(fromEnterCluster));


                    //console.log(timeCluster.name+' '+maxCountPortal+' '+ timeLeftObj.hours+' '+timeLeftObj.minutes+' '+timeLeftObj.seconds)

                    if (timeCluster.name) {
                        path.push({
                            name: timeCluster.name,
                            fromCode: timeCluster.enterFromCluster,
                            maxPortal: maxCountPortal,
                            time: timeLeftObj
                        });
                    }
                    /*
                                <template v-else-if="!timeCluster.blank">
                                    <div class="mt-2">
                                        {{timeCluster.name}}
                                        <span v-if="getEnterClusterInfo(timeCluster).isTunnel()"> ({{ getEnterClusterInfo(timeCluster).getAnyMaxEnterCount() }}) </span>
                                    </div>
                                </template>
                    * */

                });
                return path;
            },

            resetTunnelArray() {
                localStorage.setItem('tunnelArray', '');
            },
            getRemainingTimeByTunnel(tunnel) {

                if (!tunnel.nowTimeMs || !tunnel.maxTimeMs) return null;
                if (tunnel.nowTimeMs > tunnel.maxTimeMs) return null;

                let portalInfo = {
                    normal: {
                        timeLeftMs: tunnel.maxTimeMs - tunnel.nowTimeMs
                    },
                    reverse: {
                        timeLeftMs: tunnel.maxTimeMs - tunnel.nowTimeMs
                    }
                }
                return this.getRemainingTime(portalInfo);
            },
            getRemainingTime(portalInfo) {
                if (!portalInfo) return null;
                let leftTime = portalInfo.normal.timeLeftMs || portalInfo.reverse.timeLeftMs;
                let timeZoneOffset = new Date().getTimezoneOffset();
                let offset = 0;

                offset = (-300 - timeZoneOffset) * 60 * 1000;

                return new Date((+this.updateTime + leftTime)) - new Date() + offset;
            },

            getEnterClusterInfo(cluster) {
                let enterFromTNL = false;

                if (cluster.enterFromCluster) {
                    enterFromTNL = cluster.enterFromCluster.indexOf('TNL') != -1;
                }

                let fromName = "";
                let fromTimeLeft = "";
                let fromColor = "white"
                let maxEnterCount = null;
                let fromTimeLeftMs = null;

                let fromNameReverse = "";
                let fromTimeLeftReverse = "";
                let fromColorReverse = "white"
                let maxEnterCountReverse = null;
                let fromTimeLeftMsReverse = null;


                if (cluster.blank) {
                    fromColorReverse = "black"
                    fromColor = "black"
                }


                let fromPortalCluster = this.roaData[cluster.enterFromCluster]; //NORMAL
                let fromPortalClusterReverse = cluster; //REVERSE

                if (enterFromTNL) {
                    let fromPortal = fromPortalCluster.portalList[cluster.code]; //NORMAL
                    let fromPortalReverse = fromPortalClusterReverse.portalList[cluster.enterFromCluster]; // REVERSE
                    if (fromPortal) {
                        fromName = fromPortalCluster.name + ' ⯇ ' + fromPortal.maxEnterCount + ' ⯇ ' + fromPortal.name;
                        fromNameReverse = fromPortalCluster.name + ' ⯈ ' + fromPortalReverse.maxEnterCount + ' ⯈ ' + fromPortal.name;
                        fromTimeLeft = fromPortal.timeLeft;
                        fromTimeLeftReverse = fromPortalReverse.timeLeft;
                        maxEnterCount = fromPortal.maxEnterCount;
                        fromColor = this.getPortalColorByEnterNumber(fromPortal.maxEnterCount);
                        fromColorReverse = this.getPortalColorByEnterNumber(fromPortalReverse.maxEnterCount);
                        maxEnterCountReverse = fromPortalReverse.maxEnterCount;

                        if (fromPortal.nowTimeMs && fromPortal.nowTimeMs < fromPortal.maxTimeMs) {
                            fromTimeLeftMs = fromPortal.maxTimeMs - fromPortal.nowTimeMs;
                        }

                        if (fromPortalReverse.nowTimeMs && fromPortalReverse.nowTimeMs < fromPortalReverse.maxTimeMs) {
                            fromTimeLeftMsReverse = fromPortalReverse.maxTimeMs - fromPortalReverse.nowTimeMs;
                        }
                    }
                } else {
                    if (cluster.enterFromCluster)
                        fromName = fromPortalCluster.name;
                    fromNameReverse = fromPortalClusterReverse.name;
                }
                return {
                    normal: {
                        name: fromName,
                        timeLeft: fromTimeLeft,
                        color: fromColor,
                        timeLeftMs: fromTimeLeftMs,
                        maxEnterCount: maxEnterCount,
                    },
                    reverse: {
                        name: fromNameReverse,
                        timeLeft: fromTimeLeftReverse,
                        color: fromColorReverse,
                        timeLeftMs: fromTimeLeftMsReverse,
                        maxEnterCount: maxEnterCountReverse,
                    },
                    isTunnel() {
                        return !!this.getAnyMaxEnterCount();
                    },
                    getAnyMaxEnterCount() {
                        return this.reverse.maxEnterCount || this.normal.maxEnterCount;
                    }
                };
            },

            getPortalColorByEnterNumber(enterNumber) {
                if (enterNumber == 2) {
                    return "#00ff21";
                } else if (enterNumber == 7) {
                    return "#34acff";
                } else {
                    return "#ffe541";
                }
            },
            to_roman(text) {
                if (!text) return "";
                var rezult = "";
                var n = this.font_ar.length - 1;
                while (text > 0) {
                    if (text >= this.font_ar[n]) {
                        rezult += this.font_rom[n];
                        text -= this.font_ar[n];
                    } else n--;
                }
                return rezult;
            },

            doFilterResource(resourceData, withTier = false, withQuantity = false, withLocation = false, withMarker = false, withClusterType = false) {
                let needAdd = false;
                let generalNeedAdd = true;
                let needMarker = false;
                let needLocation = false;
                let needCluster = false;

                if (withLocation) {
                    this.locationFilter.forEach((locationName) => {
                        console.log(locationName, resourceData.name);
                        if (locationName == resourceData.name) {
                            needLocation = true;
                        }
                    });

                } else {
                    needLocation = true;
                }

                if (withMarker) {
                    this.markerFilter.forEach((markerName) => {
                        //console.log(locationName, resourceData.name);
                        if (resourceData.markerInfo[markerName] > 0) {
                            needMarker = true;
                        }
                    });
                } else {
                    needMarker = true;
                }

                if (withClusterType) {
                    this.clusterTypeFilter.forEach((clusterType) => {
                        //console.log(locationName, resourceData.name);
                        if (resourceData.type == clusterType) {
                            needCluster = true;
                        }
                    });
                } else {
                    needCluster = true;
                }

                if (this.resourceFilter && this.resourceFilter.length) {
                    this.resourceFilter.forEach((filterResource) => {
                        (resourceData.resourceList || []).forEach((resource) => {

                            if (filterResource === resource["name"]) {

                                needAdd = true;
                                if (withTier && needAdd) {
                                    needAdd = false;
                                    this.tierFilter.forEach((tier) => {
                                        if (tier == resource["tier"]) needAdd = true;
                                    });
                                }
                                if (withQuantity && needAdd) {
                                    needAdd = this.quantityFilter <= parseInt(resource["quantity"]);
                                    //console.log(resource,this.quantityFilter,resource["@count"], this.quantityFilter <= parseInt(resource["@count"]));
                                }

                                console.log(" ", resource["name"], needAdd)

                            }
                        });

                        console.log(resourceData.name, filterResource, needAdd)
                        if (!needAdd) {
                            generalNeedAdd = false;
                        }
                    });

                    // (resourceData || []).forEach((resource) => {
                    //
                    //     // if(!needAdd) {
                    //     //     generalNeedAdd = false;
                    //     // }
                    // });
                } else {
                    needAdd = true;
                }

                /*if (needAdd) {
                    needAdd = this.doFilterTier(resourceData.resourceList);
                }*/

                /*if (needAdd) {
                    needAdd = this.doFilterQuantity(resourceData.resourceList);
                }*/

                // if (!generalNeedAdd) return false;

                return needAdd && needLocation && needMarker && needCluster;
            },

            doFilterTier(resourceData) {
                let needAdd = false;

                if (this.tierFilter.length) {
                    (resourceData || []).forEach((resource) => {
                        this.tierFilter.forEach((filterTier) => {
                            if (filterTier == resource["tier"]) needAdd = true;
                        });
                    });
                } else {
                    needAdd = true;
                }

                return needAdd;
            },

            doFilterQuantity(resourceData) {
                let needAdd = false;

                if (parseInt(this.quantityFilter)) {
                    (resourceData || []).forEach((resource) => {
                        if (this.quantityFilter == resource["quantity"]) needAdd = true;
                    });
                } else {
                    needAdd = true;
                }

                return needAdd;
            },

            doFilteredTunnelArray() {
                let filteredTunnelArray = [];

                //this.resourceFilterLoading = true;

                this.tunnelArray.forEach((cluster) => {
                    let needAdd = false;

                    /** Ресурсы */

                    needAdd = this.doFilterResource(
                        cluster,
                        this.tierFilter.length > 0,
                        parseInt(this.quantityFilter),
                        this.locationFilter.length > 0,
                        this.markerFilter.length > 0,
                        this.clusterTypeFilter.length > 0,
                    );

                    if (needAdd) {
                        filteredTunnelArray.push(cluster);
                    }


                });
                this.filteredTunnelArray = filteredTunnelArray;
                this.resourceFilterLoading = false;
            }

        }
    }
</script>

