<template xmlns:v-slot="http://www.w3.org/1999/XSL/Transform">
    <v-menu
            v-model="menuDialog"
            :close-on-content-click="false"
            :nudge-width="250"
            offset-x

    >
        <template v-slot:activator="{ on, attrs }">
            <v-btn
                    color="success"
                    dark
                    v-bind="attrs"
                    v-on="on"
                    class="ma-1"
                    icon
            >
                <v-icon>fa-cog</v-icon>
            </v-btn>
        </template>

        <v-card>
            <v-list>
                <v-list-item>

                    <v-list-item-content>
                        <v-list-item-title>Map Settings</v-list-item-title>
                    </v-list-item-content>

                </v-list-item>
            </v-list>

            <v-divider></v-divider>

            <v-slider
                    class="ma-1 pa-1"
                    step="0.1"
                    v-model="zoneOpacity"
                    min="0"
                    max="1"
                    label="Zone Opacity"
                    thumb-label="always"
                    :thumb-size="20"
                    hide-details
            ></v-slider>

            <!--<v-slider-->
                    <!--class="ma-1 pa-1"-->
                    <!--label="Zone Icon       "-->
                    <!--v-model="zoneIcon"-->
                    <!--:tick-labels="['Yes', 'No']"-->
                    <!--:max="1"-->
                    <!--step="1"-->
                    <!--ticks="always"-->
                    <!--tick-size="1"-->
                    <!--hide-details-->
            <!--&gt;</v-slider>-->

            <!--<v-radio-group v-model="row" row class="ma-1 pa-1">-->
                <!--<span class="mr-1">Zone icon</span>-->
                <!--<v-radio label="yes" value="radio-1"></v-radio>-->
                <!--<v-radio label="no" value="radio-2"></v-radio>-->
            <!--</v-radio-group>-->
            <v-divider></v-divider>

            <v-switch
                    class="ma-1 pa-1"
                    hide-details
                    dense
                    inset
                    v-model="zoneIcon"
                    label="Zone icon">
            </v-switch>

            <v-switch
                    class="ma-1 pa-1"
                    hide-details
                    dense
                    inset
                    v-model="zoneName"
                    label="Zone name">
            </v-switch>

            <v-row>
                <v-col cols="3"> <v-checkbox class="ma-0 pa-0"dense hide-details v-model="selectedMapTier" label="I" value="1"></v-checkbox></v-col>
                <v-col cols="3"> <v-checkbox class="ma-0 pa-0"dense hide-details v-model="selectedMapTier" label="II" value="2"></v-checkbox></v-col>
                <v-col cols="3"> <v-checkbox class="ma-0 pa-0"dense hide-details v-model="selectedMapTier" label="III" value="3"></v-checkbox></v-col>
                <v-col cols="3"> <v-checkbox class="ma-0 pa-0"dense hide-details v-model="selectedMapTier" label="IV" value="4"></v-checkbox></v-col>
            </v-row>
            <v-row>
                <v-col cols="3"> <v-checkbox class="ma-0 pa-0"dense hide-details v-model="selectedMapTier" label="V" value="5"></v-checkbox></v-col>
                <v-col cols="3"> <v-checkbox class="ma-0 pa-0"dense hide-details v-model="selectedMapTier" label="VI" value="6"></v-checkbox></v-col>
                <v-col cols="3"> <v-checkbox class="ma-0 pa-0"dense hide-details v-model="selectedMapTier" label="VII" value="7"></v-checkbox></v-col>
                <v-col cols="3"> <v-checkbox class="ma-0 pa-0"dense hide-details v-model="selectedMapTier" label="VIII" value="8"></v-checkbox></v-col>
            </v-row>

            <v-card-actions>
                <v-spacer></v-spacer>

                <v-btn text @click="menuDialog = false">Close</v-btn>
            </v-card-actions>
        </v-card>
    </v-menu>
</template>

<script>
    import {mapGetters, mapMutations} from "vuex";

    export default {
        name: "MapSettings",
        data: () => ({
            menuDialog: false,
            selectedMapTier: ["1","2","3","4","5","6","7","8"],
        }),
        methods:{
          ...mapMutations({
              setZoneOpacity: 'SET_ZONE_OPACITY',
              setZoneIcon: 'SET_ZONE_ICON',
              setZoneName: 'SET_ZONE_NAME'
          })
        },
        computed:{
            ...mapGetters({
                getZoneOpacity: 'GET_ZONE_OPACITY',
                getZoneIcon: 'GET_ZONE_ICON',
                getZoneName: 'GET_ZONE_NAME',
            }),

            zoneOpacity:{
                get() {
                    return this.getZoneOpacity;
                },
                set(value) {
                    this.setZoneOpacity(value);
                }
            },

            zoneIcon:{
                get() {
                    return this.getZoneIcon;
                },
                set(value) {
                    this.setZoneIcon(value);
                }
            },

            zoneName:{
                get() {
                    return this.getZoneName;
                },
                set(value) {
                    this.setZoneName(value);
                }
            }
        }
    }
</script>

<style scoped>

</style>