export default class UserMarkerInfo {

    constructor(data = {}){
        this.name = data.name || "marker "+new Date().toISOString();
        this.latlng = data.latlng || [];
        this.description = data.description || " ";
        this.icon = (data.icon)?data.icon:null;

        if(typeof data.iconColor === "object" && data.iconColor != null){
            this.iconColor = data.iconColor;
        }else{
            let defaultHex = (data.iconColor)?data.iconColor:"#FFFFFF";
            this.iconColor = { "alpha": 1, "hex": defaultHex, rgba: UserMarkerInfo.hexToRgbA(defaultHex) };
        }

        if(typeof data.frameColor === "object" && data.frameColor != null){
            this.frameColor = data.frameColor;
        }else{
            let defaultHex = (data.frameColor)?data.frameColor:"#000000";
            this.frameColor = { "alpha": 1, "hex": defaultHex, rgba: UserMarkerInfo.hexToRgbA(defaultHex) }
        }

    }

    static iconNameList = [
        'fa-skull',
        'fa-house-user',
        'fa-leaf',
        'fa-exclamation-triangle',
        'fa-question',
        'fa-comment-dots',
        'fa-cannabis',
        'fa-frown-open',
        'fa-bell',
        'fa-radiation',
        'fa-archway',
        'fa-thumbtack',
        'fa-anchor',
        'fa-minus-circle',
        'fa-shield-alt',
        'fa-cat',
        'fa-fish',
        'fa-apple-alt',
        'fa-crow',
        'fa-horse',
        'fa-users',
        'fa-surprise',
        'fa-dizzy',
        'fa-bookmark',
        'fa-square-full',
        'fa-circle',
        'fa-check-circle',
        'fa-gem',
        'fa-chess-rook',
        'fa-user-secret',
        'fa-poop',
        'fa-poo',
        'fa-laugh',
        'fa-cog',
        'fa-flag',
        'fa-trash',
        'fa-concierge-bell',
    ]

    /**
     * Колорпикер не работает корректно без rgba
     * @param hex
     * @returns {{a: number, r: number, b: number, g: number}}
     */
    static hexToRgbA(hex){
        var c;
        if(/^#([A-Fa-f0-9]{3}){1,2}$/.test(hex)){
            c= hex.substring(1).split('');
            if(c.length== 3){
                c= [c[0], c[0], c[1], c[1], c[2], c[2]];
            }
            c= '0x'+c.join('');
            return { "r":(c>>16)&255, "g":(c>>8)&255, "b":c&255,"a": 1 };
        }
        throw new Error('Bad Hex');
    }

}