import Resource from "./Resource";

export default class Cluster {

    get isMarkerInfo() {
        let isMarkerInfo = false;
        Object.keys(this.markerInfo).forEach((key) => {
            if (!isMarkerInfo) isMarkerInfo = this.markerInfo[key] > 0;
        });

        return isMarkerInfo;
    }


    constructor(data = {}, withInfo = false) {
        if (withInfo) {
            this.id = data.id;
            this.name = data.name;
            this.tier = data.tier;
            this.type = data.type;
            this.resourceList = data.resourceList;
            this.markerInfo = data.markerInfo;

            return;
        } else {

            this.id = data["@id"];
            this.name = data["@displayname"];

            this.tier = 0;
            try {
                this.tier = data["@file"].match(/_T(.+?)_/gi)[0].match(/(\d)/gi)[0];
            } catch (e) {
            }
            ;

            this.type = data["@type"];
            this.resourceList = [];

            this.markerInfo = {
                "WOOD": 0,
                "FIBRE": 0,
                "STONE": 0,
                "ORE": 0,
                "HIDE": 0,
                "DUNGEON SOLO": 0,
                "DUNGEON GROUP": 0,
                "DUNGEON AVALON": 0,
                "GREEN CHEST": 0,
                "BLUE CHEST": 0,
                "GOLD CHEST": 0,
            };
            if (data["distribution"]) {

                let resource = data["distribution"]["resource"] || [];
                if (resource) {
                    if (!resource.length) {
                        resource = [resource];
                    }
                }

                if (this.type.indexOf('TUNNEL_') != -1) {
                    //console.log('data["distribution"]["resource"]',resource);
                    (resource).forEach((resource) => {
                        this.resourceList.push(
                            new Resource(resource)
                        );
                    })
                }
            }

            if (data['minimapmarkers']) {
                //console.log('data[\'minimapmarkers\'][\'marker\']', data['minimapmarkers']['marker']);
                if (data['minimapmarkers']['marker'] && data['minimapmarkers']['marker'].length > 0) {
                    data['minimapmarkers']['marker'].forEach((marker) => {
                        try {
                            this.markerInfo[Cluster.markerType[marker['@type']]] = this.markerInfo[Cluster.markerType[marker['@type']]] + 1;
                        } catch (e) {

                        }
                        /*let markerKey = null;
                        try {
                            console.log('Cluster.markerType', Cluster.markerType[marker['@type']]);
                            let markerKey = Cluster.markerType[marker['@type']];
                        } catch (e) {

                        }
                        // markerListType.push(marker['@type']);
                        if (markerKey !== null){
                            console.log('markerKey', markerKey)

                        }*/

                    })
                }
            }
        }
    }

    static markerType = {
        "Wood": "WOOD",
        "Fiber": "FIBRE",
        "Stone": "STONE",
        "Ore": "ORE",
        "Hide": "HIDE",
        "dungeon_solo": "DUNGEON SOLO",
        "dungeon_group": "DUNGEON GROUP",
        "dungeon_elite": "DUNGEON AVALON",
        "roads_of_avalon_solo_pve": "GREEN CHEST",
        "roads_of_avalon_group_pve": "BLUE CHEST",
        "roads_of_avalon_raid_pve": "GOLD CHEST",
    }

    static clusterTypeList = [
        "OPENPVP_YELLOW",
        "OPENPVP_RED",
        "TUNNEL_ROYAL",
        "TUNNEL_LOW",
        "TUNNEL_MEDIUM",
        "TUNNEL_HIGH",
        "TUNNEL_BLACK_LOW",
        "TUNNEL_BLACK_MEDIUM",
        "TUNNEL_BLACK_HIGH",
        "TUNNEL_DEEP",
        "TUNNEL_HIDEOUT",
        "TUNNEL_HIDEOUT_DEEP",
        "SAFEAREA",
        "OPENPVP_BLACK_1",
        "OPENPVP_BLACK_2",
        "OPENPVP_BLACK_3",
        "OPENPVP_BLACK_4",
        "OPENPVP_BLACK_5",
        "OPENPVP_BLACK_6",
        "OPENPVP_BLACK_7"
    ]
}