import Vue from 'vue';
import Vuex from 'vuex';
import * as axios from "axios";
import VueAxios from "vue-axios";

Vue.use(Vuex);
Vue.use(VueAxios, axios)

//axios.defaults.baseURL = 'http://localhost/';

export const store = new Vuex.Store({

    state: {
        VERSION: 1,                       //текущая версия контента
        VIEW_HASH: null,                  //хэш для просмотра
        EDIT_HASH: null,                  //хэш для редактирвоания
        CURRENT_LOCATION: {},             //Текущая выбранная локация (кликнутая)

        GAME_LOCATION_LIST: [],           //Все полигоны (локации)
        USER_MARKER_LIST: {BLACK: [], ROYAL: []},             //Все маркеры юзера
        USER_PATH: {BLACK: [], ROYAL: []},

        SOURCE_CODE_DIALOG: false,        //Диалоговое окно с полигонами
        POLYGON_BUILD_MODE: false,        //Режим стройки области (полигон)

        POLYLINE_PATH_BUILD_MODE: false,  //Режим стройки пути
        MARKER_BUILD_MODE: false,         //Режим установки маркеров

        CURRENT_MAP: null,                //Текущая карта (чёрные или Королевка)

        /* Настройки */
        ZONE_OPACITY: 0.5,                //Непрозрачность
        ZONE_ICON: true,                  //Иконка зоны (тип: синяя,оранж,красные,черные)
        ZONE_NAME: true,                  //Имя локации
        /* *** */
    },
    getters: {
        GET_STORE(state){
            let stateCopy = {...state};
            let stateCopyV2 = {};

            delete stateCopy.GAME_LOCATION_LIST;
            delete stateCopy.VIEW_HASH;
            delete stateCopy.EDIT_HASH;
            delete stateCopy.POLYLINE_PATH_BUILD_MODE;
            delete stateCopy.MARKER_BUILD_MODE;

            Object.keys(stateCopy).forEach((key) =>{
                stateCopyV2[key] = stateCopy[key];
            });

            return stateCopyV2;
        },
        GET_GAME_LOCATION_LIST(state) {
            return state.GAME_LOCATION_LIST;
        },
        GET_CURRENT_LOCATION(state) {
            return state.CURRENT_LOCATION;
        },
        GET_POLYGON_BUILD_MODE(state) {
            return state.POLYGON_BUILD_MODE;
        },
        GET_POLYLINE_PATH_BUILD_MODE(state) {
            return state.POLYLINE_PATH_BUILD_MODE;
        },
        GET_MARKER_BUILD_MODE(state) {
            return state.MARKER_BUILD_MODE;
        },
        GET_SOURCE_CODE_DIALOG(state) {
            return state.SOURCE_CODE_DIALOG;
        },
        GET_CURRENT_MAP(state) {
            return state.CURRENT_MAP;
        },
        GET_USER_MARKER_LIST(state) {
            return state.USER_MARKER_LIST[state.CURRENT_MAP];
        },
        GET_ZONE_OPACITY(state) {
            return state.ZONE_OPACITY;
        },
        GET_ZONE_ICON(state) {
            return state.ZONE_ICON;
        },
        GET_ZONE_NAME(state) {
            return state.ZONE_NAME;
        },
        GET_USER_PATH(state) {
            return state.USER_PATH[state.CURRENT_MAP];
        },
        GET_EDIT_HASH(state) {
            return state.EDIT_HASH;
        },
        GET_VIEW_HASH(state) {
            return state.VIEW_HASH;
        },
    },
    mutations: {
        LOAD_STORE(state, payload){
          let newStore = JSON.parse(payload);
            Object.keys(newStore).forEach((key) => {
                state[key] = newStore[key];
            })

        },
        SET_CURRENT_LOCATION(state, payload) {
            state.CURRENT_LOCATION = payload;
        },
        SET_GAME_LOCATION_LIST(state, payload) {
            state.GAME_LOCATION_LIST = payload;
        },
        SET_POLYGON_BUILD_MODE(state, payload) {
            state.POLYGON_BUILD_MODE = !!payload;
        },
        SET_POLYLINE_PATH_BUILD_MODE(state, payload) {
            state.POLYLINE_PATH_BUILD_MODE = !!payload;
            if(payload)state.MARKER_BUILD_MODE = !payload;
        },
        SET_MARKER_BUILD_MODE(state, payload) {
            state.MARKER_BUILD_MODE = !!payload;
            if(payload)state.POLYLINE_PATH_BUILD_MODE = !payload;
        },
        SET_SOURCE_CODE_DIALOG(state, payload) {
            state.SOURCE_CODE_DIALOG = !!payload;
        },
        SET_CURRENT_MAP(state, payload) {
            state.CURRENT_MAP = payload;
        },
        SET_USER_MARKER_LIST(state, payload) {
            let needAdd = true;
            if(!state.USER_MARKER_LIST[state.CURRENT_MAP]){
                state.USER_MARKER_LIST[state.CURRENT_MAP] = [];
            }

            if(!payload || payload.length === 0){
                state.USER_MARKER_LIST[state.CURRENT_MAP] = [];
            }

            //Поиск одинаковых маркеров
            for(let i = 0; i < state.USER_MARKER_LIST[state.CURRENT_MAP].length; i++){
                let item =  state.USER_MARKER_LIST[state.CURRENT_MAP][i];

                if(JSON.stringify(item.latlng) == JSON.stringify(payload.latlng)){
                    state.USER_MARKER_LIST[state.CURRENT_MAP][i] = payload;
                    needAdd = false;
                }
            }

            if(needAdd) state.USER_MARKER_LIST[state.CURRENT_MAP].push(payload);
        },
        SET_ZONE_OPACITY(state, payload) {
            state.ZONE_OPACITY = payload;
        },
        SET_ZONE_ICON(state, payload) {
            state.ZONE_ICON = payload;
        },
        SET_ZONE_NAME(state, payload) {
            state.ZONE_NAME = payload;
        },
        SET_USER_PATH(state, payload) {
            if(!state.CURRENT_MAP) return;

            if(!state.USER_PATH[state.CURRENT_MAP]){
                state.USER_PATH[state.CURRENT_MAP] = [];
            }
            state.USER_PATH[state.CURRENT_MAP] = payload;
        },
        ADD_USER_PATH(state, payload) {
            if(!state.CURRENT_MAP) return;

            if(!state.USER_PATH[state.CURRENT_MAP]){
                state.USER_PATH[state.CURRENT_MAP] = [];
            }
            console.log('ADD_USER_PATH USER_PATH',state.USER_PATH[state.CURRENT_MAP]);
            state.USER_PATH[state.CURRENT_MAP].push(payload);
        },
        SET_VIEW_HASH(state, payload){
            state.VIEW_HASH = payload;
        },
        SET_EDIT_HASH(state, payload){
            state.VIEW_HASH = payload;
        }
    },
    actions: {
        /**
         *
         * @returns {Promise<void>}
         * @constructor
         */
        async REQUEST_SAVE_DATA_ON_SERVER({commit},toSave){

            let requestBody = {
                method: 'POST',
                data: toSave
            };

            console.log('requestBody', requestBody);
            let saveResult = await (axios.request(
                'content.php', requestBody, {'Content-Type': 'application/json;charset=utf-8'}
            ));

            if(!saveResult.data.success){
                alert("REQUEST_SAVE_TO_SERVER ERROR: "+saveResult.data.error);
            }

            return saveResult.data;
        },

        async REQUEST_DATA_FROM_SERVER({commit}, param){

            let requestBody = {
                method: 'GET',
            };

            console.log('requestBody', requestBody);
            let getResult = await (axios.request(
                'content.php?view_hash='+param, requestBody, {'Content-Type': 'application/json;charset=utf-8'}
            ));


            if(!getResult.data.success){
                alert("REQUEST_DATA_FROM_SERVER ERROR: "+getResult.data.error);
            }

            return getResult.data;
        },
        async REQUEST_ROA_DATA_FROM_SERVER({commit}, param){

            let requestBody = {
                method: 'GET',
            };

            console.log('requestBody', requestBody);
            let getResult = await (axios.request(
                'roa-data.php?uid='+param, requestBody, {'Content-Type': 'application/json;charset=utf-8'}
            ));


            if(!getResult.data.success){
                alert("REQUEST_ROA_DATA_FROM_SERVER ERROR: "+getResult.data.error);
            }

            return getResult.data;
        }
    }
});