<template xmlns:v-slot="http://www.w3.org/1999/XSL/Transform">
    <div class="text-center" style="z-index: 9999">
        <!-- МЕНЮ ДЛЯ ПОЛЬЗОВАТЕЛЕЙ -->

        <SaveUrlMenu></SaveUrlMenu>

        <!--<v-tooltip bottom>-->
            <!--<template v-slot:activator="{ on, attrs }">-->
                <!--<v-btn dark fab class="mr-3"-->
                       <!--color="green"-->
                       <!--@click="saveDataOnServer"-->
                       <!--v-on="on"-->
                       <!--small>-->
                    <!--<v-icon>fa-save</v-icon>-->
                <!--</v-btn>-->
            <!--</template>-->
            <!--<span>Save on server1</span>-->
        <!--</v-tooltip>-->

        <!-- Режим Установка маркера -->
        <v-tooltip bottom>
            <template v-slot:activator="{ on, attrs }">
                <v-btn dark fab class="mr-1"
                       :color="markerBuildMode?'green':'red'"
                       @click="setMarkerBuildMode(!markerBuildMode)"
                       v-on="on"
                       small>
                    <v-icon>fa-map-marker-alt</v-icon>
                </v-btn>
            </template>
            <span>Toggle marker mode</span>
        </v-tooltip>

        <!-- Переключение режима строки пути -->
        <v-tooltip bottom>
            <template v-slot:activator="{ on, attrs }">
                <v-btn dark fab class="mr-1"
                       :color="polylinePathBuildMode?'green':'red'"
                       @click="setPolylinePathBuildMode(!polylinePathBuildMode)"
                       v-on="on"
                       small>
                    <v-icon>fa-drafting-compass</v-icon>
                </v-btn>
            </template>
            <span>Toggle path mode</span>
        </v-tooltip>
        <!-- Чистка пути -->
        <v-tooltip bottom>
            <template v-slot:activator="{ on, attrs }">
                <v-btn fab class="mr-1"
                       color="yellow"
                       @click="clearAllBuildMode"
                       v-on="on"
                       small>
                    <v-icon color="black">fa-trash-alt</v-icon>
                </v-btn>
            </template>
            <span>Clear</span>
        </v-tooltip>

        <v-select
                v-model="selectedMap"
                class="menu-element mr-1"
                style="max-width: 100px"
                :items="mapItemList"
                label="Map"
                outlined
                dense
                @change="onCurrentMapChange"
        ></v-select>
        <v-autocomplete
                style="min-width: 300px;"
                class="menu-element mt-5"
                outlined
                dense
                :item-text="['game','name']"
                :item-value="[]"
                :items="gameLocationList"
                @change="changeAutocompleteLocation"
                label="Location"
        >
            <!--<template v-slot:selection="{data,item}" :input-value="data.selected">-->
            <!--{{item.game.name}}-->
            <!--</template>-->

            <template v-slot:item="{data,item}">
                <v-btn style="background:gray" icon small readonly class="mr-1">
                    <v-icon class="mark" :style="{color: item.leaflet.color }">fa-bookmark</v-icon>
                </v-btn>
                {{item.game.name}}
            </template>
        </v-autocomplete>

        <!-- Настрйока прозрачности -->
        <MapSettings></MapSettings>
        <!-- МЕНЮ РАЗРАБОТКИ -->
        <template v-if="$route.query.dev">
            <v-badge
                    bordered
                    overlap
                    :content="gameLocationList.length"
                    offset-x="20"
            >
                <v-btn color="success ml-5" @click="setSourceCodeDialog(true)">SRC</v-btn>
            </v-badge>
            <v-menu
                    v-model="menu"
                    :close-on-content-click="false"
                    :nudge-width="200"
                    offset-x

            >

                <template v-slot:activator="{ on, attrs }">
                    <v-btn
                            color="success"
                            dark
                            v-bind="attrs"
                            v-on="on"
                            class="ma-5"
                            icon
                    >
                        <v-icon>fa-wrench</v-icon>
                    </v-btn>
                </template>

                <v-card>
                    <v-list>
                        <v-list-item>
                            <!--<v-list-item-avatar>-->
                            <!--<img src="https://cdn.vuetifyjs.com/images/john.jpg" alt="John">-->
                            <!--</v-list-item-avatar>-->

                            <v-list-item-content>
                                <v-list-item-title>Меню разработки</v-list-item-title>
                                <!--<v-list-item-subtitle>Founder of Vuetify.js</v-list-item-subtitle>-->
                            </v-list-item-content>

                            <!--<v-list-item-action>-->
                            <!--<v-btn-->
                            <!--:class="fav ? 'red&#45;&#45;text' : ''"-->
                            <!--icon-->
                            <!--@click="fav = !fav"-->
                            <!--&gt;-->
                            <!--<v-icon>mdi-build</v-icon>-->
                            <!--</v-btn>-->
                            <!--</v-list-item-action>-->
                        </v-list-item>
                    </v-list>

                    <v-divider></v-divider>

                    <!--<v-list>-->
                    <!--<v-list-item>-->
                    <!--<v-list-item-action>-->
                    <!--<v-switch v-model="message" color="purple"></v-switch>-->
                    <!--</v-list-item-action>-->
                    <!--<v-list-item-title>Режим постройки области</v-list-item-title>-->
                    <!--</v-list-item>-->
                    <!--<v-card>-->
                    <v-btn width="95%" @click="setPolygonBuildMode(!polygonBuildMode)" class="ma-1"
                           :class="[polygonBuildMode?'success':'error' ]" small>Режим постройки области
                    </v-btn>
                    <br>
                    <v-btn width="95%" @click="setPolylinePathBuildMode(!polygonBuildMode)" class="ma-1"
                           :class="[polylinePathBuildMode?'success':'error' ]" small>Режим постройки пути
                    </v-btn>
                    <br>

                    <!--</v-card>-->


                    <!--<v-list-item>-->
                    <!--<v-list-item-action>-->
                    <!--<v-switch v-model="hints" color="purple"></v-switch>-->
                    <!--</v-list-item-action>-->
                    <!--<v-list-item-title>Enable hints</v-list-item-title>-->
                    <!--</v-list-item>-->
                    <!--</v-list>-->

                    <v-card-actions>
                        <v-spacer></v-spacer>

                        <v-btn text @click="menu = false">Закрыть</v-btn>
                    </v-card-actions>
                </v-card>
            </v-menu>
        </template>
        <!--<Road></Road>-->
    </div>
</template>

<script>
    import {mapActions, mapGetters, mapMutations} from "vuex";
    import bus from '../bus/';
    import mapCodeBlack from '../map/mapCodeBlack';
    import mapCodeRoyal from '../map/mapCodeRoyal';
    import GameLocation from "../model/GameLocation";
    import LeafletOption from "../model/LeafletOption";
    import GameInfo from "../model/GameInfo";
    import MapSettings from "./MapSettings";
    import SaveUrlMenu from "./SaveUrlMenu";
    import Road from "./Road";


    export default {
        name: "Menu",
        components: {Road, SaveUrlMenu, MapSettings},
        data: () => ({
            selectedMap: null,
            mapItemList: [
                {text: "Black", value: "BLACK"},
                {text: "Royal", value: "ROYAL"}
            ],
            menu: false
        }),
        mounted() {

            let map = this.$route.query.map;
            if (map) {
                this.setCurrentMap(map);
                this.selectedMap = map;
                this.onCurrentMapChange(map);
            } else {
                this.onCurrentMapChange('BLACK');
            }
            //
            // let view = this.$route.query.view_hash;
            //
            // if(view){
            //     this.requestGetData(view).then((response) =>{
            //         this.loadStore(response.data.content);
            //     });
            //
            // }
        },

        methods: {
            ...mapActions({
               requestSaveData:'REQUEST_SAVE_DATA_ON_SERVER',
               requestGetData:'REQUEST_DATA_FROM_SERVER',
            }),
            ...mapMutations({
                setPolygonBuildMode: 'SET_POLYGON_BUILD_MODE',
                setPolylinePathBuildMode: 'SET_POLYLINE_PATH_BUILD_MODE',
                setMarkerBuildMode: 'SET_MARKER_BUILD_MODE',
                setSourceCodeDialog: 'SET_SOURCE_CODE_DIALOG',
                setCurrentMap: 'SET_CURRENT_MAP',
                setGameLocationList: 'SET_GAME_LOCATION_LIST',
                loadStore: 'LOAD_STORE',
            }),

            saveDataOnServer(){
                let toSave = {
                        name: 'test',
                        content: this.getStore,
                };

                this.requestSaveData(toSave);

            },

            changeAutocompleteLocation(gameLocation) {
                bus.$emit('CAMERA_GAME_LOCATION', gameLocation);
            },

            clearAllBuildMode() {
                bus.$emit('CLEAR_ALL_BUILD_MODE');
            },

            /**
             * При смены типа карты
             * @param mapCode
             */
            onCurrentMapChange(code) {
                let mapCode = null,
                    polygonList = [],
                    pushQuery = null;

                this.setCurrentMap(code);
                this.selectedMap = code;

                pushQuery = {query: {map: code}};
                pushQuery = {query: Object.assign({...this.$route.query} || {}, pushQuery.query)};

                //Меняем параметры запроса натип карты
                this.$router.push(pushQuery).catch(() => {
                });


                //В зависимости от типа карты формируем нужные списки полигонов
                if (code === "ROYAL") {
                    mapCode = mapCodeRoyal;
                } else if (code === "BLACK") {
                    mapCode = mapCodeBlack;
                }

                if (mapCode != null && mapCode.length > 0) {
                    mapCode.forEach((data) => {
                        polygonList.push(new GameLocation(new LeafletOption(data.leaflet), new GameInfo(data.game)));
                    })
                }

                this.setGameLocationList(polygonList);
            }

        },
        computed: {
            ...mapGetters({
                polylinePathBuildMode: 'GET_POLYLINE_PATH_BUILD_MODE',
                polygonBuildMode: 'GET_POLYGON_BUILD_MODE',
                markerBuildMode: 'GET_MARKER_BUILD_MODE',
                gameLocationList: 'GET_GAME_LOCATION_LIST',
                getStore: 'GET_STORE',
            }),

        }
    }
</script>

<style>
    .menu-element {
        display: inline-block;
    }

    .mark {
        text-shadow: 2px 2px;
    }

</style>