<template xmlns:v-slot="http://www.w3.org/1999/XSL/Transform">
    <div style="height: 100%; width: 100%">

        <!-- Ипорт / экспорт -->
        <v-dialog
                v-model="sourceCodeDialog"
                filled
                width="70%"
                style="z-index:9999;"
                @click:outside="setSourceCodeDialog(false)"

        >

            <v-card class="pa-5">
                <v-row style="width:100%">
                    <v-col cols="6">
                        <p>Нажмите на кнопку "Копировать код" и в буфер обмена попадёт исходный код карты </p>
                        <v-textarea
                                outlined
                                label="Экспорт"
                                :value="JSON.stringify(gameLocationList,null,1)"
                                id="exportCode"
                        ></v-textarea>
                        <v-btn class="success" @click="clipboardSourceCode">Копировать код</v-btn>
                    </v-col>
                    <v-col cols="6">
                        <p>Вставте код в окно "Импорт" и нажмите на кнопку "Загрузить код", тогда ваши изменения
                            добавятся к текущей карте</p>
                        <v-textarea
                                v-model="importSourceCodeText"
                                outlined
                                label="Импорт"
                        ></v-textarea>
                        <v-btn class="success" @click="importSourceCode">Загрузить код</v-btn>
                        <v-btn class="error" @click="importSourceCodeText = ''">Отчистить</v-btn>
                    </v-col>

                </v-row>

            </v-card>

        </v-dialog>

        <!-- Устанвока маркера -->
        <v-dialog
                v-model="userMarkerDialog"
