                                hide-details
                                class="mt-1"
                                v-model="userMarkerBuildInfo.icon"
                                outlined
                                dense
                                label="Icon"
                                :item-value="[]"
                                :items="getUserMarkerList"
                        >
                            <template v-slot:item="{data,item}">
                                <v-btn icon small readonly class="mr-1">
                                    <v-icon size="30"> {{item}}</v-icon>
                                </v-btn>
                            </template>
                        </v-autocomplete>
                        <!--<v-select-->
                        <!--v-if="gameKey == 'fraction'"-->
                        <!--v-model="buildModePolygonInfo.game[gameKey]"-->
                        <!--:items="['EMPTY','THETFORD','MARTLOCK','BRIDGEWATCH','LYMHURST','FORT_STERLING']"-->
                        <!--label="Фракция"-->
                        <!--:key="gameKey"-->
                        <!--&gt;</v-select>-->
                    </template>

                    <v-row algin="center" justify="center" v-if="userMarkerBuildInfo.icon && markerBuildMode" >
                        <v-col cols="6">

                            <v-card flat outlined class="pa-1">
                                <v-card-title>Frame color</v-card-title>
                                <v-card-text>
                                    <v-color-picker
                                            v-model="userMarkerBuildInfo.frameColor"
                                            :disabled="!userMarkerBuildInfo.icon"
                                            :show-swatches="!!userMarkerBuildInfo.icon"
                                            hide-canvas
                                            hide-inputs
                                            hide-mode-switch></v-color-picker>
                                </v-card-text>
                            </v-card>
                        </v-col>

                        <v-col cols="6">
                            <v-card flat outlined class="pa-1">
                                <v-card-title>Icon color</v-card-title>
                                <v-card-text>
                                    <v-color-picker v-model="userMarkerBuildInfo.iconColor"
                                                    :disabled="!userMarkerBuildInfo.icon"
                                                    :show-swatches="!!userMarkerBuildInfo.icon"
                                                    hide-canvas
                                                    hide-inputs
                                                    hide-mode-switch>
                                    </v-color-picker>
                                </v-card-text>
                            </v-card>
                        </v-col>

                        <v-btn :color="userMarkerBuildInfo.frameColor.hex || 'black'" fab small>
                            <v-icon :color="userMarkerBuildInfo.iconColor.hex || 'white' ">{{userMarkerBuildInfo.icon}}
                            </v-icon>
                        </v-btn>
                    </v-row>
                    <template v-if="userMarkerBuildInfo.icon">
                        <v-text-field v-model="userMarkerBuildInfo.name" :clearable="markerBuildMode" class="mt-1" outlined dense :readonly="!markerBuildMode"
                                      label="Marker name"></v-text-field>
                        <v-textarea v-model="userMarkerBuildInfo.description" :clearable="markerBuildMode" outlined dense :readonly="!markerBuildMode"
                                    label="Marker Description"></v-textarea>
                    </template>
                    <v-card-actions v-if="markerBuildMode">
                        <v-spacer></v-spacer>
                        <v-btn class="error" @click="userMarkerDialog = false">Cancel</v-btn>
                        <v-btn v-if="userMarkerBuildInfo.icon" @click="addUserMarker" class="success">Save</v-btn>
                    </v-card-actions>


                </v-card-text>
            </v-card>
        </v-dialog>

        <!-- Настройка области (модальное окно) -->
        <v-dialog
                v-model="changePolygonInfoDialog"
                width="500"
                persistent
                style="z-index:9999;"

        >
            <v-card>
                <v-card-title
                        class="grey lighten-2"
                        primary-title
                >
                    Настройка области
                </v-card-title>

                <v-card-text>

                    <template v-for="gameKey in Object.keys(buildModePolygonInfo.game)">
                        <v-select
                                v-if="gameKey == 'fraction'"
                                v-model="buildModePolygonInfo.game[gameKey]"
                                :items="['EMPTY','THETFORD','MARTLOCK','BRIDGEWATCH','LYMHURST','FORT_STERLING']"
                                label="Фракция"
                                :key="gameKey"
                        ></v-select>
                        <v-select
                                v-else-if="gameKey == 'tier'"
                                v-model="buildModePolygonInfo.game[gameKey]"
                                :items="[1,2,3,4,5,6,7,8]"
                                label="Уровень локации (tier)"
                                :key="gameKey"
                        ></v-select>
                        <v-select
                                v-else-if="gameKey == 'type'"
                                v-model="buildModePolygonInfo.game[gameKey]"
                                :items="['BLUE','RED','ORANGE','BLACK']"
                                label="Тип локации"
                                :key="gameKey"
                        ></v-select>
                        <v-select
                                v-else-if="gameKey == 'biom'"
                                v-model="buildModePolygonInfo.game[gameKey]"
                                :items="['FOREST','HIGHLAND','MOUNTAIN','STEPPE','SWAMP']"
                                label="Биом"
                                :key="gameKey"
                        ></v-select>
                        <v-text-field v-else
                                      v-model="buildModePolygonInfo.game[gameKey]"
                                      :label="gameKey"
                                      :key="gameKey"
                        >

                        </v-text-field>
                    </template>
                    <!--<v-divider></v-divider>-->
                    <!--<v-text-field v-model="buildModePolygonInfo.leaflet[leafletKey]" :label="leafletKey" v-for="leafletKey in Object.keys(buildModePolygonInfo.leaflet)" :key="leafletKey"></v-text-field>-->
                </v-card-text>

                <v-divider></v-divider>

                <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn
                            color="primary"
                            text
                            @click="closePolygonBuildMode"
                    >
                        Закрыть
                    </v-btn>
                    <v-btn
                            color="success"
                            text
                            @click="createGameLocationInfoFromBuildMode"
                    >
                        Сохранить
                    </v-btn>
                    <v-btn
                            color="error"
                            text
                            @click="clearBuildModePolygonInfo(false)"
                    >
                        Удалить
                    </v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>

        <l-map
                v-if="showMap"
                :zoom="zoom"
                :maxZoom="maxZoom"
                :minZoom="2"
                :center="center"
                :options="mapOptions"
                style="z-index: 0;"
                @update:center="centerUpdate"
                @update:zoom="zoomUpdate"
                v-on:click="mapClick"
                minLvl="0"

        >

            <l-tile-layer
                    :url="getCurrentUrl"
                    :attribution="attribution"
                    :no-wrap="true"
            />

            <template v-if="!userMarkerDialog">
            <!-- Строим путь -->
            <l-polyline :lat-lngs="getUserPath" :color="'#00ffd1'"></l-polyline>

            <!-- Полигон для режима постройки -->
            <l-polygon :lat-lngs="buildModPolygonLatLng"
                       :color="`#00f2ff`"
                       @click="innerClick(buildModePolygonInfo)">
            </l-polygon>

            <l-polygon v-for="polygon in gameLocationList"
                       :key="JSON.stringify(polygon.leaflet.latlngs)+'POLYGON'"
                       :lat-lngs="polygon.leaflet.latlngs"
                       :color="polygon.leaflet.color"
                       :opacity="getZoneOpacity"
                       :fillOpacity="getZoneOpacity/3"
                       :fillColor="polygon.leaflet.fillColor"
                       @click="innerClick(polygon)"
                       style="z-index:1000"
            >

            </l-polygon>

            <!-- Маркер для иконок -->
            <template v-if="getZoneIcon">
                <l-marker v-for="polygon in gameLocationList"
                          :key="JSON.stringify(polygon.leaflet.latlngs)+'MARKER'"
                          :lat-lng="polygon.leaflet.latlngs[1]">
                    <l-icon
                            :icon-anchor="[25, 10+currentZoom*7]"
                            :icon-size="[0, 37]">
                        <div class="headline">{{to_roman(polygon.game.tier)}}</div>
                        <!--<div class="map-name" :style="{ top: 135 + 2*currentZoom+'px'}" >TEXT</div>-->
                        <img :width="50" :src="getRiskLevelByPolygon(polygon)"/>
                    </l-icon>
                </l-marker>
            </template>

            <!-- Маркер для наименования карты -->
            <template v-if="getZoneName && currentZoom > 4 ">
                <l-marker v-for="polygon in gameLocationList"
                          :key="JSON.stringify(polygon.leaflet.latlngs)+'MAP_NAME'"
                          :lat-lng="getCenterPointByPolygonLatLngList(polygon.leaflet.latlngs)"
                          style="z-index:2">
                    <l-icon
                            :icon-anchor="[25, 10+currentZoom*7]"
                            :icon-size="[0, 37]">
                        <div class="map-name">{{polygon.game.name}}</div>
                        <span class="map-tier" v-if="!getZoneIcon">{{to_roman(polygon.game.tier)}}</span>
                        <!--<img :width="50" src="https://albionmap.000webhostapp.com/images/black-zone-icon.png"/>-->
                    </l-icon>
                </l-marker>
            </template>

            <!-- Маркер пользователя (установочный) -->
            <l-marker v-if="userMarkerLatLng"
                      :key="'USER_MARKER_SETUP'"
                      :lat-lng="userMarkerLatLng">
                <l-icon
                        :icon-anchor="[20, 20]"
                        :icon-size="[0, 0]">
                    <v-btn color="black" fab x-small>
                        <v-icon color="black">fa-circle</v-icon>
                    </v-btn>
                </l-icon>

                <!--<l-tooltip :options="{direction: 'top',opacity: 0.67}"><span class="subtitle-1">Типа маркер</span></l-tooltip>-->
            </l-marker>

            <!-- Сохранённые маркеры -->
            <template v-for="userMarker in userMarkerList">
                <l-marker v-if="userMarker.latlng"
                          :key="'USER_MARKER_SETUP'+userMarker.name"
                          :lat-lng="userMarker.latlng"
                          @click="userMarkerClick(userMarker)"
                >
                    <l-icon
                            :icon-anchor="[20, 20]"
                            :icon-size="[0, 0]">
                        <v-btn
                                :color="userMarker.frameColor.hex" fab x-small>
                            <v-icon :color="userMarker.iconColor.hex">{{userMarker.icon}}</v-icon>
                        </v-btn>
                    </l-icon>

                    <l-tooltip :options="{direction: 'top',opacity: 0.67}"><span class="subtitle-1">{{userMarker.name}}</span>

                    </l-tooltip>
                </l-marker>
            </template>

            <!--<l-marker-->
            <!--v-for="marker in markerList" :key="marker.name+marker.coords"-->
            <!--:lat-lng="marker.coords"-->
            <!--&gt;-->
            <!--<l-icon :iconSize="[100,100]"-->
            <!--:icon-url="marker.imgUrl"-->
            <!--class-name="someExtraClass">-->
            <!--<div>-->
            <!--<div class="text-with-shadow" :class="(!marker.name)?'hidden-name':''">{{ marker.name || 1 }}-->
            <!--</div>-->
            <!--</div>-->
            <!--<img class="icon-img" :src="marker.imgUrl">-->
            <!--</l-icon>-->
            <!--</l-marker>-->


            <!--            <l-circle-->
            <!--                    v-for="marker in markerList" :key="marker.name"-->
            <!--                    :lat-lng="marker.coord"-->
            <!--                    :radius="100"-->
            <!--                    fillColor="red"-->
            <!--                    :fillOpacity="1"-->
            <!--                    color="red"-->
            <!--            >-->
            <!--                <l-popup><img-->
            <!--                        src="https://cdn.discordapp.com/attachments/261894158157873170/692054366978441266/1232.png">-->
            <!--                    <p>asdasdasd asd asd asd asd asd </p>-->
            <!--                </l-popup>-->
            <!--            </l-circle>-->

            </template>
        </l-map>
    </div>
</template>

<script>
    import {latLng, latLngBounds} from "leaflet";
    import {LIcon, LMap, LMarker, LPolygon, LPolyline, LTileLayer, LTooltip} from "vue2-leaflet";
    import {mapGetters, mapMutations} from "vuex";
    import GameLocation from "../model/GameLocation";
    import LeafletOption from "../model/LeafletOption";
    import GameInfo from "../model/GameInfo";
    import UserMarkerInfo from "../model/UserMarkerInfo";
    import bus from '../bus/';
    import mapCodeBlack from '../map/mapCodeBlack';
    import mapCodeRoyal from '../map/mapCodeRoyal';
    import { Base64 } from 'js-base64';

    export default {
        name: 'LeafletMap',
        components: {
            LMap,
            LMarker,
            LTileLayer,
            LIcon,
            LPolygon,
            LPolyline,
            latLngBounds,
            LTooltip
            // LCircle,
            // LPopup,
        },
        mounted() {

            bus.$on("CAMERA_GAME_LOCATION", (payLoad) => {
                let leaflet = payLoad.leaflet;
                this.center = {lat: leaflet.latlngs[0][0], lng: leaflet.latlngs[3][1]};
            });

            //Чистка постройки
            bus.$on("CLEAR_ALL_BUILD_MODE", () => {
                //this.buildPolylinePathLatLng = [];
                this.setUserPath([]);
                this.userMarkerBuildInfo = new UserMarkerInfo();

                this.setPath(null);
                this.resetMarker();

            });

            // if (this.$route.query.path) {
            //     this.buildPolylinePathLatLng = JSON.parse(Base64.decode(this.$route.query.path));
            // }

            //МАРКЕРЫ
            if (this.$route.query.marker & 1 == 2) {
                let markerInfoList = JSON.parse(Base64.decode(this.$route.query.marker));
                let userMarkerList = [];

                if(markerInfoList.length > 0){

                    markerInfoList.forEach((markerInfo) =>{
                        userMarkerList.push(new UserMarkerInfo(markerInfo));
                    })
                }

                this.setUserMarkerList(userMarkerList);
            }

            //
            //
            // if (this.getCurrentMapCode != null && this.getCurrentMapCode.length > 0) {
            //     this.getCurrentMapCode.forEach((data) => {
            //         this.polygonList.push(new GameLocation(new LeafletOption(data.leaflet), new GameInfo(data.game)));
            //     })
            // }
            //
            // this.setGameLocationList(this.polygonList);

        },
        // watch:{
        //     userMarkerDialog : (newValue,oldValue) => {
        //         if(newValue === false){
        //
        //         }
        //     }
        // },
        data() {

            return {
                //lu LatLng {lat: 85.11141578062661, lng: -205.6640625}
                //ld LatLng {lat: -66.37275500247456, lng: -205.6640625}
                //ru LatLng {lat: 84.92832092949963, lng: 109.68750000000001}
                //rd LatLng {lat: -67.06743335108298, lng: 109.68750000000001}
                // bounds: latLngBounds([
                //     [85.11141578062661, -305.6640625],
                //     [-66.37275500247456, -305.6640625]
                // ]),
                // maxBounds: latLngBounds([
                //     [84.92832092949963, 9.68750000000001],
                //     [-67.06743335108298, 9.68750000000001]
                // ]),
                swatches: [
                    ['#FF0000', '#AA0000', '#550000'],
                    ['#FFFF00', '#AAAA00', '#555500'],
                    ['#00FF00', '#00AA00', '#005500'],
                    ['#00FFFF', '#00AAAA', '#005555'],
                    ['#0000FF', '#0000AA', '#000055'],
                ],
                userMarkerDialog: false,
                importSourceCodeText: '',
                changePolygonInfoDialog: false,
                //buildPolylinePathLatLng: [],
                buildModPolygonLatLng: [],
                buildModePolygonInfo: new GameLocation(),
                userMarkerBuildInfo: new UserMarkerInfo(),

                font_ar: [1, 4, 5, 9, 10, 40, 50, 90, 100, 400, 500, 900, 1000, 4000, 5000, 9000, 10000],
                font_rom: ["I", "IV", "V", "IX", "X", "XL", "L", "XC", "C", "CD", "D", "CM", "M",
                    "M&#8577;", "&#8577;", "&#8577;&#8578;", "&#8578;"],

                markerList: [
                    /*                   {name: 'Коробка', description: '', imgUrl: 'https://image.flaticon.com/icons/svg/679/679821.svg',
                                           coords: latLng(71.70969140544025, -40.37443481864955)},    */
                    {
                        name: 'МАРКЕР',
                        description: '',
                        imgUrl: 'https://albiononline2d.ams3.cdn.digitaloceanspaces.com/thumbnails/orig/T1_FISH_FRESHWATER_ALL_COMMON',
                        coords: latLng(68.04046125904843,
                            70.31250000000001)
                    },
                    /*{description: '', imgUrl: 'https://imgur.com/LMtaQ6E.png',
                        coords: latLng(78.9489467464742, -90.42368403701974)},*/

                ],
                zoom: 1,
                center: latLng(47.41322, -1.219482),
                // url: 'https://albionmap.000webhostapp.com/map/{z}-{x}-{y}.jpg',
                url: null,
                blackUrl: 'http://347136-cx85213.tmweb.ru/albion-map/{z}-{x}-{y}.jpg',
                royalUrl: 'http://347136-cx85213.tmweb.ru/albion-map-royal/{z}-{x}-{y}.jpg',
                attribution:
                    '&copy; <a href="http://osm.org/copyright">OpenStreetMap</a> contributors',
                selectedUserMarkerIcon: null,
                userMarkerLatLng: null,
                withPopup: latLng(47.41322, -1.219482),
                withTooltip: latLng(157.41422, -1.250482),
                currentZoom: 1,
                maxZoom: 7,
                currentCenter: latLng(47.41322, -1.219482),
                showParagraph: false,

                polygonList: [],
                mapOptions: {
                    zoomSnap: 0.5
                },
                recordForPolygon: [],
                showMap: true,
                mapCode: [],
            };
        },
        computed: {
            ...mapGetters({
                getUserPath: 'GET_USER_PATH',
                polylinePathBuildMode: 'GET_POLYLINE_PATH_BUILD_MODE',
                polygonBuildMode: 'GET_POLYGON_BUILD_MODE',
                sourceCodeDialog: 'GET_SOURCE_CODE_DIALOG',
                gameLocationList: 'GET_GAME_LOCATION_LIST',
                currentMap: 'GET_CURRENT_MAP',
                markerBuildMode: 'GET_MARKER_BUILD_MODE',
                userMarkerList: 'GET_USER_MARKER_LIST',
                getZoneOpacity: 'GET_ZONE_OPACITY',
                getZoneIcon: 'GET_ZONE_ICON',
                getZoneName: 'GET_ZONE_NAME'
            }),
            getCurrentUrl() {
                let url = '';
                if (this.currentMap === "ROYAL") {
                    url = this.royalUrl;
                } else if (this.currentMap === "BLACK") {
                    url = this.blackUrl;
                }
                return url
            },
            getCurrentMapCode() {
                if (this.currentMap === "ROYAL") {
                    this.mapCode = mapCodeRoyal;
                } else if (this.currentMap === "BLACK") {
                    this.mapCode = mapCodeBlack;
                }
            },

            getUserMarkerList() {
                return UserMarkerInfo.iconNameList;
            }
            // maxBounds() {
            //     var width = 21268;
            //     var height = 17217;
            //     //var maxLevel = 7;
            //     //var minLevel = 0;
            //     var orgLevel = 7;
            //
            //     // Some weird calculations to fit the image to the initial position
            //     // Leaflet has some bugs there. The fitBounds method is not correct for large scale bounds
            //     var tileWidth = 256 * Math.pow(2, orgLevel);
            //     var radius = tileWidth / 2 / Math.PI;
            //     var rx = width - tileWidth / 2;
            //     var ry = -height + tileWidth / 2;
            //
            //     var west = -180;
            //     var east = (180 / Math.PI) * (rx / radius);
            //     var north = 85.05;
            //     var south = (360 / Math.PI) * (Math.atan(Math.exp(ry / radius)) - (Math.PI / 4));
            //     //var rc = (tileWidth / 2 + ry) / 2;
            //     //var centerLat = (360 / Math.PI) * (Math.atan(Math.exp(rc / radius)) - (Math.PI / 4));
            //     //var centerLon = (west + east) / 2;
            //
            //     return [[south, west], [north, east]];
            // }
        },
        methods: {
            ...mapMutations({
                setPolylinePathBuildMode: 'SET_POLYLINE_PATH_BUILD_MODE',
                setCurrentLocation: 'SET_CURRENT_LOCATION',
                setSourceCodeDialog: 'SET_SOURCE_CODE_DIALOG',
                setGameLocationList: 'SET_GAME_LOCATION_LIST',
                setUserMarkerList: 'SET_USER_MARKER_LIST',
                addUserPath: 'ADD_USER_PATH',
                setUserPath: 'SET_USER_PATH',
            }),
            to_roman(text) {
                if (!text) return "";
                var rezult = "";
                var n = this.font_ar.length - 1;
                while (text > 0) {
                    if (text >= this.font_ar[n]) {
                        rezult += this.font_rom[n];
                        text -= this.font_ar[n];
                    } else n--;
                }
                return rezult;
            },
            to_arab(text1) {
                var text = text1.toUpperCase();
                var rezult = 0;
                var posit = 0;
                var n = this.font_ar.length - 1;
                while (n >= 0 && posit < text.length) {
                    if (text.substr(posit, this.font_rom[n].length) == this.font_rom[n]) {
                        rezult += this.font_ar[n];
                        posit += this.font_rom[n].length;
                    } else n--;
                }
                return rezult;
            },

            userMarkerClick(marker){
                this.userMarkerDialog = true;
                this.userMarkerBuildInfo = marker;
                this.userMarkerLatLng = null;
            },

            importSourceCode() {
                let newMapList = JSON.parse(this.importSourceCodeText);

                newMapList.forEach((newMap) => {
                    let needAdd = true;

                    //Если нет соотвествующих параметров, не добавляем
                    this.gameLocationList.forEach((oldMap) => {
                        if (needAdd && !newMap.leaflet && !newMap.game) {
                            needAdd = false;
                            return
                        }

                        if (needAdd) {
                            if (newMap.game.name.trim() === oldMap.game.name.trim()) {
                                needAdd = false;
                            }
                        }
                    });

                    if (needAdd) {
                        this.gameLocationList.push(new GameLocation(new LeafletOption(newMap.leaflet), new GameInfo(newMap.game)));
                    }


                })

            },
            clipboardSourceCode() {
                // let exportCode = this.$refs.exportCode.$el.querySelector('input')
                // exportCode.select()
                // document.execCommand("copy");
                let input = document.getElementById("exportCode");
                input.select();
                document.execCommand("copy");
            },

            getRiskLevelByPolygon(polygon) {
                if (!polygon || !polygon.game.type) return null;
                return GameInfo.staticRiscLevel[polygon.game.type].iconUrl;
            },

            getCenterPointByPolygonLatLngList(latlngList) {
                return [latlngList[0][0], latlngList[3][1]];
            },
            resetMarker(){
                // let resetMarkerQuery = {query: {...this.$route.query}};
                //
                // if(resetMarkerQuery.query.marker){
                //     delete resetMarkerQuery.query.marker;
                //     this.$router.push(resetMarkerQuery).catch(() => {
                //     });
                // }
                this.setUserMarkerList([]);
            },

            addUserMarker(){
                let markerInfo = {...this.userMarkerBuildInfo};

                this.setUserMarkerList(markerInfo);
                this.userMarkerBuildInfo = new UserMarkerInfo();
                this.userMarkerDialog = false;

                //console.log('JSON.stringify(markerInfo)',JSON.stringify(this.userMarkerList,null,1));

                //let markerQuery = {query: {marker: Base64.encode(JSON.stringify(this.userMarkerList))}};
                //markerQuery = {query: Object.assign({...this.$route.query} || {}, markerQuery.query)};

                //Удаляем путь, если пришёл null
                //if (!markerInfo || !markerInfo.latlng) {
                //    delete markerQuery.query.marker;
                //}

                //this.$router.push(markerQuery).catch(() => {
                //});

                this.userMarkerLatLng = null;

                console.log('this.userMarkerList',this.userMarkerList);
            },

            //btoa atob
            setPath(pathLatLng) {

                //let pushQuery = {query: {path: Base64.encode(pathLatLng)}};
                //pushQuery = {query: Object.assign({...this.$route.query} || {}, pushQuery.query)};

                //Удаляем путь, если пришёл null
                //if (!pathLatLng || !pathLatLng.length) {
                //    delete pushQuery.query.path;
                //}

                //this.$router.push(pushQuery).catch(() => {
                //});
                //this.$router.push({query: {path: btoa(pathLatLng)}});
            },

            mapClick(event) {

                let latlng = event.latlng;

                console.log(latlng);
                //this.center = {lat: latlng.lat, lng: latlng.lng};

                let polygonDot = [];
                polygonDot.push(latlng.lat);
                polygonDot.push(latlng.lng);
                this.recordForPolygon.push(polygonDot);

                if (this.buildModPolygonLatLng.length < 4 && this.polygonBuildMode) {
                    this.buildModPolygonLatLng.push(polygonDot);

                } else if (this.polylinePathBuildMode) {

                    //this.buildPolylinePathLatLng.push(polygonDot);
                    this.addUserPath([...polygonDot]);
                    //this.setPath(JSON.stringify(this.buildPolylinePathLatLng));
                    //console.log('buildPolylinePathLatLng', this.buildPolylinePathLatLng);
                } else if (this.markerBuildMode) {
                    this.userMarkerDialog = true;
                    this.userMarkerBuildInfo = new UserMarkerInfo({latlng: latlng});
                    this.userMarkerLatLng = latlng;
                }


                //console.log('recordForPolygon', JSON.stringify(this.recordForPolygon, null, 1));


                //console.log('latLng('+event.latlng.lat+', '+event.latlng.lng+')');
                // this.markerList.push(
                //     {
                //         description: 'test',
                //         name: 'test',
                //         imgUrl: 'https://imgur.com/LMtaQ6E.png',
                //         coords: latLng(event.latlng.lat, event.latlng.lng)
                //     }
                //     );
                //latLng(71.70969140544025, -40.37443481864955)
            },

            getLatLng(x, y) {
                return latLng(x, y) //47.41322 -1.219482
            },
            zoomUpdate(zoom) {
                //this.zoom = (zoom > 3) ? 3 : zoom;
                console.log('zoomUpdate', zoom);
                this.currentZoom = zoom;

            },
            centerUpdate(center) {
                this.currentCenter = center;
            },
            showLongText() {
                this.showParagraph = !this.showParagraph;
            },
            closePolygonBuildMode() {
                this.buildModPolygonLatLng = [];
                this.changePolygonInfoDialog = false
            },
            innerClick(polygon) {
                if (this.polygonBuildMode) {
                    this.innerClickPolygonBuildMode(polygon)
                } else {
                    this.setCurrentLocation({name: polygon.game.name, tier: this.to_roman(polygon.game.tier)})
                }

            },
            innerClickPolygonBuildMode(polygon) {
                if (polygon.game != null && polygon.game.name) {
                    this.buildModPolygonLatLng = polygon.leaflet.latlngs;
                    this.buildModePolygonInfo = polygon;
                } else {
                    this.buildModePolygonInfo.leaflet.latlngs = this.buildModPolygonLatLng;
                }
                //alert(polygon.name + " (T" + polygon.tier + ")");
                this.changePolygonInfoDialog = true;
                //this.setCurrentLocation({name: polygon.game.name, tier: this.to_roman(polygon.game.tier)})
            },
            clearBuildModePolygonInfo(afterSave) {
                this.changePolygonInfoDialog = false;

                if (!afterSave) {
                    this.gameLocationList.forEach((polygon) => {
                        if (JSON.stringify(polygon.leaflet.latlngs) === JSON.stringify(this.buildModePolygonInfo.leaflet.latlngs)) {

                            this.gameLocationList.splice(this.gameLocationList.indexOf(polygon), 1);
                        }
                    });
                }

                this.buildModPolygonLatLng = [];
                this.buildModePolygonInfo = new GameLocation();
            },


            createGameLocationInfoFromBuildMode() {
                let polygonInList = false;
                let copyBuildModePolygonInfo = Object.assign({}, this.buildModePolygonInfo);
                let gameLocation = new GameLocation(
                    new LeafletOption(
                        copyBuildModePolygonInfo.leaflet.latlngs
                    ),
                    new GameInfo(
                        copyBuildModePolygonInfo.game.name,
                        copyBuildModePolygonInfo.game.tier,
                        copyBuildModePolygonInfo.game.type,
                        copyBuildModePolygonInfo.game.fraction,
                        copyBuildModePolygonInfo.game.biom
                    )
                );

                this.gameLocationList.forEach((polygon) => {
                    if (JSON.stringify(polygon.leaflet.latlngs) === JSON.stringify(this.buildModePolygonInfo.leaflet.latlngs)) {
                        polygonInList = true;
                        polygon = gameLocation;
                    }
                });

                if (!polygonInList) {
                    console.log('this.gameLocationList before', this.gameLocationList);
                    this.gameLocationList.push(gameLocation);
                    console.log('this.gameLocationList after', this.gameLocationList);
                }
                window.localStorage.setItem('backup_' + new Date().toISOString(), JSON.stringify(this.gameLocationList));

                this.clearBuildModePolygonInfo(true);

            }


        },
    }


</script>

